.title-bar {
  position: absolute;
  top: 0;
  left: 0;
  width: 230px;
  height: 100%;
  &__wrapper {
    width: 230px;
    margin-top: 11px;
    border: 1px solid $thunder;
    border-left: 0;
    border-bottom: 0;
  }
  &__title {
    position: absolute;
    top: -10px;
    left: 0;
    color: $thunder;
    font-size: 14px;
    font-weight: normal;
    line-height: 20px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    margin: 0 0 15px;
    padding-right: 12px;
    background: $white;
  }
  &__list {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 25px 0 20px;
    list-style: none;
    counter-reset: item;
  }
  &__item {
    display: flex;
    margin-bottom: 15px;
    counter-increment: item;
    cursor: pointer;
    &::before {
      content: counter(item) '.';
      color: $hurricane;
      margin-right: 6px;
      transition: .25s;
    }
    &:last-child {
      margin-bottom: 0;
    }
    &:hover, &--active {
      &::before {
        color: $thunder;
      }
      .title-bar__link {
        color: $thunder;
      }
    }
  }
  &__link {
    display: block;
    color: $hurricane;
    transition: .25s;
  }
}

.sticky {
  position: sticky;
  top: 30px;
  &::before, &::after {
    content: '';
    display: table;
  }
}