.article-page,
.news-view {
  h2,
  h3 {
    color: $thunder;
    font-weight: 500;
    margin: 0;
    @include media(">=tablet") {
      padding: 0 30px;
    }
  }
  h2 {
    line-height: 30px;
    font-size: 21px;
    margin: 40px 0 3px;
    @include media(">=tablet") {
      line-height: 35px;
      font-size: 25px;
      margin-top: 27px;
    }
    @include media(">=desktop") {
      margin-top: 35px;
    }
  }
  h3 {
    line-height: 25px;
    font-size: 18px;
  }
  p {
    font-family: "PT Serif";
    color: $thunder;
    font-size: 16px;
    font-weight: normal;
    line-height: 25px;
    margin-bottom: 9px;
    @include media(">=tablet") {
      padding: 0 30px;
      font-size: 18px;
      line-height: 30px;
    }
  }
  ol,
  ul {
    margin: 0;
    padding: 0 30px 0 20px;
    @include media(">=tablet") {
      padding: 0 30px 0 50px;
    }
  }
  ul {
    list-style-type: disc;
  }
  &__content {
    .container {
      @include media(">=tablet") {
        max-width: 720px;
      }
    }
  }
  &__top {
    .container {
      @include media(">=tablet") {
        max-width: 720px;
      }
    }
    @include media(">=tablet") {
      padding: 45px 0 40px;
      background: $white;
    }
    @include media(">=desktop") {
      padding-bottom: 20px;
    }
    &-wrapper {
      position: relative;
      padding: 30px 0 20px;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 50%;
        width: 100vw;
        height: 100%;
        margin-left: -50vw;
        background: $pampas;
        z-index: -1;
      }
      @include media(">=tablet") {
        position: static;
        padding: 0 30px;
        &::before {
          display: none;
        }
      }
      .social {
        display: block;
        margin-top: 10px;
        @include media(">=desktop") {
          display: none;
          margin-top: 20px;
        }
      }
    }
  }
  &__content-wrapper {
    position: relative;
    padding-bottom: 25px;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      width: 100vw;
      height: 100%;
      margin-left: -50vw;
      display: block;
      background: $pampas;
      z-index: -1;
    }
    @include media(">=tablet") {
      padding-bottom: 0;
      &::before {
        display: none;
      }
    }
  }
  &__wrapper {
    padding-bottom: 80px;
  }
  &__main {
    position: relative;
  }
  &__title-main {
    position: relative;
    margin-bottom: 40px;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      width: 100vw;
      height: 100%;
      margin-left: -50vw;
      background: $white;
      border-bottom: 1px solid $swirl;
      z-index: 1;
    }
    @include media(">=tablet") {
      margin-bottom: 35px;
      &::before {
        display: none;
      }
    }
  }
  &__paragraph {
    display: block;
    position: relative;
    color: $thunder;
    font-family: "PT Serif";
    line-height: 30px;
    font-size: 18px;
    font-weight: normal;
    margin: 0;
    padding-bottom: 20px;
    z-index: 2;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 5px;
      margin-left: -30px;
      background-color: $thunder;
      display: none;
      @include media(">=tablet") {
        display: block;
      }
    }
    @include media(">=tablet") {
      padding: 0 30px 30px;
      line-height: 35px;
      font-size: 22px;
      &::before {
        display: none;
      }
    }
  }
  &__categories {
    @include media(">=tablet") {
      padding: 0 30px;
    }
  }
  .section-dots {
    &__title {
      color: $thunder;
      font-weight: 500;
      font-size: 21px;
      line-height: 30px;
      margin: 0;
      @include media(">=tablet") {
        font-weight: bold;
        font-size: 55px;
        line-height: 60px;
      }
    }
  }
  .comment__text {
    padding: 0;
  }
  .item__title {
    margin: 0;
    padding: 0;
  }
  .info-news__list {
    padding: 0;
  }
  .categories__list,
  .social__list {
    list-style-type: none;
    padding: 0;
  }
  .popular__list,
  .template {
    list-style-type: none;
    padding: 0;
  }
  .article {
    &__uppercase-text {
      text-transform: uppercase;
    }
    &__content {
      &-video-container,
      &-table,
      &-slider,
      &-image-description {
        margin-bottom: 40px;
        @include media(">=tablet") {
          margin-bottom: 30px;
        }
      }
      &-addon {
        @include media(">=tablet") {
          margin-bottom: 30px;
        }
      }
    }
    &__content-image-description {
      position: relative;
      font-weight: normal;
      padding: 17px 0 10px;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: calc(100% + 40px);
        height: 100%;
        margin-left: -20px;
        display: block;
        background: $white;
        z-index: -1;
      }
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        width: calc(100% + 40px);
        margin-left: -20px;
        height: 1px;
        background: $swirl;
      }
      @include media(">=tablet") {
        border-bottom: 5px solid $thunder;
        margin-left: 0;
        width: auto;
        &::before,
        &::after {
          display: none;
        }
      }
    }
    &__content-image-description,
    &__content-image-author,
    &__content-slide-item-description {
      font-size: 14px;
      line-height: 20px;
    }
    &__content-image-author {
      left: 20px;
      color: $hurricane;
      display: block;
      @include media(">=tablet") {
        display: inline-block;
      }
    }
  }
  .slick {
    &-dots {
      list-style-type: none;
      padding: 0;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      li {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width: 12px;
        height: 12px;
        margin-right: 10px;
        &:last-child {
          margin-right: 0;
        }
        &.slick-active {
          button {
            width: 12px;
            height: 12px;
            background: $crimson;
          }
        }
        button {
          font-size: 0;
          width: 10px;
          height: 10px;
          margin: 0;
          padding: 0;
          border: 0;
          background: $swirl;
          border-radius: 50%;
          outline: none;
          display: block;
          cursor: pointer;
        }
      }
    }
    &-prev,
    &-next {
      position: absolute;
      top: 130px;
      width: 30px;
      height: 30px;
      background: $thunder;
      border: 0;
      font-size: 0;
      list-style: 0;
      cursor: pointer;
      z-index: 5;
      &::after {
        content: "";
        position: absolute;
        top: 50%;
        border-style: solid;
        border-color: $white;
        border-top-width: 0px;
        border-right-width: 1px;
        border-bottom-width: 1px;
        border-left-width: 0px;
        display: inline-block;
        padding-top: 5px;
        padding-right: 5px;
        padding-bottom: 5px;
        padding-left: 5px;
        transform: translateY(-50%);
      }
      @include media(">=tablet") {
        top: 215px;
      }
    }
    &-prev {
      left: 0;
      &::after {
        left: 12px;
        transform: translateY(-50%) rotate(135deg);
      }
    }
    &-next {
      right: 0;
      &::after {
        right: 12px;
        transform: translateY(-50%) rotate(-45deg);
      }
    }
    &-list {
      img {
        height: 290px;
        @include media(">=tablet") {
          height: 460px;
        }
      }
    }
  }
}

.article-page {
  .content-insert__text {
    font-family: "PT Serif";
    color: $thunder;
    font-size: 18px;
    font-weight: normal;
    line-height: 30px;
  }
  &__social {
    .social {
      position: absolute;
      top: 0;
      right: 20px;
      top: 45px;
      display: none;
      z-index: 3;
      @include media(">=desktop") {
        display: block;
      }
      &__list {
        padding: 0;
        list-style-type: none;
        flex-direction: column;
      }
      &__item {
        margin: 0 0 10px;
      }
      &__link {
        display: block;
      }
      &__icon {
        margin: 0 0 1px;
      }
    }
  }
  &__social {
    bottom: 0;
  }
  &__inner {
    position: relative;
    .social {
      position: relative;
      display: block;
      margin-top: 10px;
      @include media(">=desktop") {
        display: none;
        margin-top: 20px;
      }
    }
  }
  &__span {
    display: block;
  }
  &__banner {
    position: absolute;
    right: -250px;
    display: none;
    @include media(">=big_desktop") {
      display: block;
    }
    &--small {
      top: 445px;
      width: 200px;
      height: 230px;
    }
    &--medium {
      top: 1245px;
      width: 200px;
      height: 330px;
    }
    &-span {
      color: $hurricane;
      line-height: 20px;
      font-size: 14px;
      text-align: center;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      display: block;
      margin-top: 10px;
    }
  }
  &__title {
    color: $thunder;
    font-weight: 500;
    line-height: 35px;
    font-size: 25px;
    margin: 0 0 5px 0;
  }
  &__text {
    margin-bottom: 13px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .title-bar {
    position: absolute;
    left: -250px;
    display: none;
    @include media(">=big_desktop") {
      display: block;
    }
    &__item {
      &::before {
        display: none;
      }
    }
    &__title {
      font-size: 14px;
      font-weight: normal;
      line-height: 20px;
    }
    &__title {
      padding: 0 15px 0 0;
    }
    &__list {
      padding: 25px 20px 20px;
    }
  }
}

.article__link {
  font-weight: 600;
  color: $crimson;
}

.article__content-addon {
  position: relative;
  padding: 37px 0;
  background: transparent;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    width: 100vw;
    height: 100%;
    margin-left: -50vw;
    background: $white;
    border-top: 1px solid $swirl;
    border-bottom: 1px solid $swirl;
    z-index: 1;
  }
  @include media(">=tablet") {
    border: 5px solid $thunder;
    border-left: 0;
    margin-top: 35px;
    padding: 35px 35px 35px 10px;
    &::before {
      display: none;
    }
  }
  &-type {
    position: absolute;
    top: 8px;
    right: -5px;
    color: $hurricane;
    line-height: 20px;
    font-size: 14px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    z-index: 2;
    @include media(">=tablet") {
      top: -25px;
      color: $thunder;
    }
  }
  &-header {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    background: $white;
    padding-right: 17px;
    margin-bottom: 25px;
    z-index: 2;
    @include media(">=tablet") {
      position: absolute;
      top: -40px;
      left: 0;
      margin-bottom: 0;
    }
    &-image {
      width: 70px;
      height: 70px;
      border-radius: 50%;
      margin-right: 20px;
    }
    &-text {
      display: flex;
      flex-direction: column;
    }
    &-author,
    &-position {
      color: $thunder;
      line-height: 20px;
      font-size: 14px;
    }
    &-author {
      font-weight: 500;
    }
  }
  &-wrapper {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    z-index: 2;
  }
  &-image {
    flex-shrink: 0;
    display: none;
    @include media(">=tablet") {
      display: block;
    }
  }
  &-text {
    font-family: "PT Serif";
    font-weight: normal;
    font-size: 16px;
    line-height: 25px;
    @include media(">=tablet") {
      font-size: 22px;
      line-height: 35px;
      margin-left: 30px;
    }
  }
  .italic-text {
    font-style: italic;
  }
}

.article__content-related-topic {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin: 15px 0;
  @include media(">=tablet") {
    padding: 0 30px;
  }
  &-source {
    position: absolute;
    top: -15px;
    left: 28px;
    color: $hurricane;
    font-size: 16px;
    line-height: 25px;
    display: block;
    &.hidden {
      display: none;
    }
    @include media(">=tablet") {
      left: 58px;
    }
  }
  &-link {
    color: $thunder;
    margin-left: 13px;
    z-index: 2;
    &:hover {
      text-decoration: underline;
    }
  }
}

.article__content-image {
  position: relative;
  width: calc(100% + 40px);
  margin-left: -20px;
  height: 270px;
  @include media(">=tablet") {
    height: 460px;
  }
  @include media(">=tablet") {
    width: 100%;
    margin-left: 0;
  }
}

.article__content-video-container {
  position: relative;
  width: calc(100% + 40px);
  margin-left: -20px;
  height: 0px;
  padding-bottom: 56.25%;
  @include media(">=tablet") {
    width: 100%;
    margin-left: 0;
  }
}

.article__content-video {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.article__content-table {
  position: relative;
  width: calc(100% + 40px);
  margin-left: -20px;
  border-collapse: collapse;
  text-align: left;
  border-spacing: 0;
  background: transparent;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    width: 100vw;
    height: 100%;
    margin-left: -50vw;
    display: block;
    background: $white;
    border: 1px solid $swirl;
    border-left: 0;
    border-right: 0;
    z-index: 1;
  }
  @include media(">=tablet") {
    width: 100%;
    border: 5px solid $thunder;
    margin-left: 0;
    &::before {
      display: none;
    }
  }
  thead {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      width: 100vw;
      height: 33%;
      margin-left: -50vw;
      display: block;
      border-bottom: 1px solid $swirl;
      z-index: 1;
    }
    @include media(">=tablet") {
      border-bottom: 5px solid $thunder;
      &::before {
        display: none;
      }
    }
  }
  th,
  td {
    color: $thunder;
    font-family: "PT Serif";
    font-size: 14px;
    line-height: 20px;
    font-weight: normal;
    padding: 10px 20px;
    flex-shrink: 0;
    width: 80px;
    z-index: 2;
    @include media(">=tablet") {
      font-size: 16px;
      line-height: 20px;
      padding: 20px 20px;
    }
  }
  tr {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  th {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  tbody {
    padding: 15px 0;
  }
}

.article__content {
  &-slider {
    position: relative;
    height: 482px;
    width: calc(100% + 40px);
    margin-left: -20px;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      width: 100vw;
      height: 100%;
      margin-left: -50vw;
      background: $white;
      display: block;
      z-index: 0;
    }
    &::after {
      content: "";
      position: absolute;
      left: 50%;
      bottom: 0;
      width: 100vw;
      margin-left: -50vw;
      border-bottom: 1px solid $swirl;
    }

    @include media(">=tablet") {
      height: 578px;
      width: 100%;
      margin-left: 0;
      border-bottom: 5px solid #231f20;
      &::before {
        display: none;
      }
      &::after {
        left: 0;
        width: 100%;
        margin-left: 0;
      }
    }

    &__layers {
      position: relative;
      height: 447px;
      z-index: 1;
      @include media(">=tablet") {
        height: 550px;
      }
    }

    &__slide {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      opacity: 0;
      visibility: hidden;
      transition: 0.5s ease;

      &_active {
        visibility: visible;
        opacity: 1;
      }

      &__image {
        width: 100%;
        height: 280px;
        object-fit: contain;
        background: black;
        @include media(">=tablet") {
          height: 460px;
        }
      }

      &__description-container {
        display: flex;
        flex-direction: column;
        padding: 0 20px;
        @include media(">=tablet") {
          flex-direction: row;
          padding: 0;
        }
      }

      &__counter {
        font-weight: 600;
        color: $hurricane;
        display: block;
        text-align: left;
        min-width: 94px;
        margin-top: 8px;
        font-family: "PT Serif", sans-serif;
        font-size: 20px;
        line-height: 30px;
        @include media(">=tablet") {
          text-align: center;
          margin-top: 22px;
        }

        .active {
          color: $thunder;
          font-weight: 600;
          font-size: 26px;
        }
      }

      &__description {
        margin-top: 9px;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        @include media(">=tablet") {
          padding-left: 16px;
        }
        &-text {
          font-size: 14px;
          line-height: 20px;
          max-height: 80px;
          overflow: hidden;
          @include media(">=tablet") {
            max-height: 55px;
          }
        }
      }

      &__author {
        color: #918184;
        margin-top: 3px;
        font-size: 14px;
        line-height: 20px;
        @include media(">=tablet") {
          margin-top: 7px;
        }
      }
    }

    &__button {
      position: absolute;
      top: 185px;
      width: 30px;
      height: 30px;
      background: rgba(35, 31, 32, 0.75);
      cursor: pointer;
      display: none;

      @include media(">=tablet") {
        display: block;
      }

      &::after {
        content: " ";
        position: relative;
        top: 9px;
        display: block;
        width: 11px;
        height: 11px;
        transform: rotate(45deg);
        border: 1px solid white;
      }

      &_prev {
        left: 0;

        &::after {
          left: 12px;
          border-width: 0px 0px 1px 1px;
        }
      }

      &_next {
        right: 0;

        &::after {
          left: 6px;
          border-width: 1px 1px 0px 0px;
        }
      }
    }

    &__paging-wrap {
      display: flex;
      justify-content: center;
    }

    &__paging {
      position: relative;
      width: 100px;
      height: 20px;

      &__sliding-wrap {
        position: absolute;
        display: flex;
        height: 20px;
        transition: 0.5s ease;
      }

      &__dot-wrap {
        position: relative;
        width: 20px;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      &__dot {
        position: absolute;
        top: 10px;
        left: 10px;
        width: 0;
        height: 0;
        background: rgba(215, 209, 203, 0);
        transition: 0.5s ease;
      }

      &__dot-wrap_d2 .article__content-slider__paging__dot {
        top: 6px;
        left: 6px;
        width: 8px;
        height: 8px;
        border-radius: 4px;
        background: #d7d1cb;
      }

      &__dot-wrap_d1 .article__content-slider__paging__dot {
        top: 5px;
        left: 5px;
        width: 10px;
        height: 10px;
        border-radius: 5px;
        background: #d7d1cb;
      }

      &__dot-wrap_d0 .article__content-slider__paging__dot {
        top: 4px;
        left: 4px;
        width: 12px;
        height: 12px;
        border-radius: 6px;
        background: #e3151a;
      }
    }
  }
}
