.navbar {
  @include media(">=desktop") {
    position: relative;
    left: 50%;
    width: 100vw;
    margin-left: -50vw;
  }
  @include media(">=big_desktop") {
    position: static;
    left: 0;
    width: 100%;
    margin-left: 0;
  }
  &__wrapper {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
    align-items: center;
    padding: 15px 0;
    @include media(">=tablet") {
      padding: 7px 0;
      justify-content: space-between;
    }
    @include media(">=desktop") {
      padding: 0 16px;
    }
    @include media(">=big_desktop") {
      padding: 0 0;
      justify-content: space-between;
    }
  }
  &__nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-right: 20px;
    @include media(">=desktop") {
      margin-right: 0;
    }
  }
  &__list {
    display: block;
    @include media(">=desktop") {
      display: flex;
    }
  }
  &__content {
    position: fixed;
    right: 0;
    bottom: -100%;
    width: 100%;
    max-height: 100vh;
    margin-right: 0;
    transition: 0.3s;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    z-index: 100;
    @include media(">=desktop") {
      position: relative;
      width: auto;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-right: 20px;
      overflow: visible;
      z-index: 5;
    }
    &--mobile-active {
      bottom: 0;
    }
  }
  &__item {
    background: $white;
    margin-right: 0;
    cursor: pointer;
    &--submenu {
      @include media(">=desktop") {
        margin-left: 15px;
      }
    }
    &:hover {
      .navbar__link {
        background: $pampas;
      }
    }
    @include media(">=desktop") {
      background: transparent;
      &:hover {
        background: transparent;
        .navbar__link {
          color: $swirl;
          background: transparent;
        }
      }
    }
    &--active {
      background: $pampas;
      @include media(">=desktop") {
        .navbar__link {
          background: $crimson;
        }
        &:hover {
          .navbar__link {
            color: $white;
            background: $crimson;
          }
        }
      }
    }
  }
  &__link {
    position: relative;
    color: $thunder;
    padding: 10px 0 5px 20px;
    border-top: 1px solid $swirl;
    width: 100%;
    display: block;
    line-height: 25px;
    transition: 0.25s;
    @include media(">=desktop") {
      font-weight: 500;
      color: $white;
      padding: 17px;
      border: none;
      width: auto;
      &::after {
        display: block;
      }
    }
  }
  &__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include media(">=tablet") {
      position: relative;
      left: auto;
      transform: translate(0);
      margin-right: auto;
    }
    @include media(">=desktop") {
      margin: 0 65px 0 0;
    }
    @include media(">=big_desktop") {
      margin: 0;
    }
  }
  &__mobile-title {
    display: block;
    background: $pampas;
    padding: 25px 0 20px 20px;
    @include media(">=desktop") {
      display: none;
    }
  }
  &__span {
    color: $thunder;
    font-weight: 500;
  }
  &__more {
    stroke: $white;
    display: block;
    @include media(">=desktop") {
      display: none;
    }
  }
}

.submenu {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  &:hover {
    .submenu__list {
      visibility: visible;
      opacity: 1;
    }
  }
  &__list {
    position: relative;
    padding: 0;
    width: 100%;
    transition: 0.2s;
    &::before {
      content: "";
      position: absolute;
      top: -6px;
      right: 6px;
      border-right: 6px solid transparent;
      border-left: 6px solid transparent;
      border-bottom: 6px solid $white;
    }
    @include media(">=desktop") {
      position: absolute;
      top: 50px;
      right: 0;
      visibility: hidden;
      border-radius: 2px;
      width: auto;
      min-width: 120px;
      opacity: 0;
      box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.3);
      padding: 10px 0;
      background: $white;
    }
  }
  &__item {
    transition: 0.25s;
    @include media(">=desktop") {
      text-align: center;
    }
    &:hover {
      .submenu__link {
        background: $pampas;
      }
    }
  }
  &__link {
    color: $thunder;
    padding: 10px 0 5px 20px;
    display: block;
    border-top: 1px solid $swirl;
    white-space: nowrap;
    @include media(">=desktop") {
      padding: 3px 20px;
      border: none;
    }
  }
  &__more {
    stroke: $white;
    display: block;
    height: 6px;
    margin-top: -2px;
    display: none;
    @include media(">=desktop") {
      display: block;
    }
    .more-svg {
      vertical-align: top;
    }
  }
}

.logo {
  &__link {
    display: block;
  }
}

.logo-svg {
  width: 72px;
  height: 38px;
}

.search {
  &__button {
    display: flex;
    cursor: pointer;
  }
  &__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    visibility: hidden;
    opacity: 0;
    z-index: 9999;
    transition: 0.25s;
    cursor: pointer;
    &--visible {
      visibility: visible;
      opacity: 1;
      .search__form {
        top: 170px;
        @include media(">=tablet") {
          top: 230px;
        }
        @include media(">=desktop") {
          top: 300px;
        }
      }
    }
  }
  &__form {
    position: absolute;
    top: 200px;
    left: 50%;
    width: 280px;
    transform: translate(-50%, 0);
    transition: 0.25s;
    @include media(">=tablet") {
      width: 620px;
      top: 260px;
    }
    @include media(">=desktop") {
      top: 330px;
    }
  }
  &__input {
    position: relative;
    width: 100%;
    background: transparent;
    border: none;
    border-radius: 0;
    border-bottom: 2px solid $white;
    outline: none;
    font-family: "Fira Sans";
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    color: $westar;
    padding: 0 10px 0 43px;
  }
  &__button-submit {
    height: 30px;
    background: transparent;
    border: 0;
    padding: 0;
    cursor: pointer;
    &--input {
      position: absolute;
      top: 0;
      left: 0;
      width: 30px;
      height: 30px;
      &::after {
        content: "";
        position: absolute;
        top: 3px;
        right: -5px;
        display: block;
        height: 80%;
        width: 1px;
        background: $white;
      }
    }
  }
  &__banner {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
  }
}

.search-svg {
  width: 30px;
  height: 30px;
  fill: none;
}

.more {
  cursor: pointer;
}

.more-svg {
  width: 25px;
  height: 6px;
  fill: transparent;
}
