.comments {
  background: $pampas;
  display: none;
  @include media('>=tablet') {
    display: block;
  }
  .container {
    @include media('>=tablet') {
      max-width: 660px;
      padding: 0 20px;
    }
  }
  &__wrapper {
    padding: 35px 0;
  }
  &__empty {
    margin-bottom: 20px;
  }
  &__title {
    color: $thunder;
    font-weight: 500;
    line-height: 35px;
    font-size: 25px;
    margin: 0 0 20px;
  }
  &__form {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-end;
    margin-bottom: 20px;
  }
  &__input {
    &.input-dk {
      padding: 6px 10px;
    }
    margin: 0 0 20px;
  }
  .comment {
    margin-bottom: 10px;
    padding-top: 17px;
    border-top: 1px solid rgba(35, 31, 32, 0.5);
    &:first-child {
      border-top: 0;
      padding-top: 0;
    }
    &--answer {
      margin: 0;
      display: none;
      &-visible {
        display: block;
      }
    }
    &__wrapper {
      margin-left: 47px;
    }
    &__content {
      margin-bottom: 10px;
    }
    &__header {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 5px;
    }
    &__avatar {
      width: 36px;
      height: 36px;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 10px;
      background: $westar;
      flex-shrink: 0;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }
    &__name {
      color: $thunder;
      font-weight: 600;
      line-height: 25px;
      font-size: 16px;
      margin: 0;
      flex-grow: 1;
    }
    &__time {
      color: $hurricane;
      line-height: 20px;
      font-size: 14px;
    }
    &__footer {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
    &__reply {
      border: 0;
      font-weight: 500;
      color: $hurricane;
      line-height: 25px;
      font-size: 16px;
      margin-left: auto;
      padding: 0;
    }
    &__text {
      font-family: 'Fira Sans';
      font-size: 16px;
      line-height: 30px;
      margin-bottom: 5px;
    }
    &__svg {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      margin-right: 2px;
      margin-bottom: 2px;
    }
    .angle-small-svg {
      transition: .20s;
      transform: rotate(180deg);
    }
    &__answers {
      position: relative;
      color: $hurricane;
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      padding: 0;
      border: 0;
      display: flex;
      flex-direction: row;
      align-items: center;
      &--visible {
        .angle-small-svg {
          transform: rotate(0);
        }
      }
    }
  }
}

.angle-small-svg {
  width: 12px;
  height: 12px;
  fill: transparent;
}

.avatar-svg {
  width: 15px;
  height: 18px;
}
