.news-feed {
  position: relative;
  padding: 25px 0 40px 0;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    width: 100vw;
    height: 100%;
    display: block;
    margin-left: -50vw;
    background: $pampas;
    z-index: -1;
  }
  @include media('>=tablet') {
    width: 280px;
    padding: 0;
    order: unset;
    &::before {
      display: none;
    }
  }
  @include media('>=big_desktop') {
    margin-left: 0;
  }
  &__title {
    color: $white;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    background: $crimson;
    margin: 0;
    padding: 5px 9px 3px;
  }
  &__list {
    margin-bottom: 23px;
  }
  &__item {
    position: relative;
    padding: 11px 0 11px;
    border-bottom: 1px solid $tapa;
    &::before,
    &::after {
      content: '';
      position: absolute;
      bottom: -3px;
      width: 5px;
      height: 5px;
      background-color: $swirl; 
    }
    &::before {
      right: 0;
    }
    &::after {
      left: 0;
    }
    &:hover {
      .news-feed__link {
        text-decoration: underline;
      }
    }
  }
  &__link {
    position: relative;
    display: block;
    color: $thunder;
  }
  &__more {
    color: $thunder;
    font-size: 16px;
    line-height: 30px;
    transition: .15s;
    &:hover {
      color: $crimson;
      text-decoration: underline;
    }
  }
  .lent-separator-svg {
    margin-top: 7px;
  }
}

.lent-separator-svg {
  display: block;
}