.slider {
  background: $white;
  padding: 40px 0;
  overflow: hidden;
  @include media('>=tablet') {
    background: $pampas;
  }
  &__link-top {
    margin-bottom: 40px;
    display: none;
    @include media('>=tablet') {
      display: block;
    }
  }
  &__link {
    position: relative;
    color: $thunder;
    font-size: 14px;
    line-height: 25px;
    padding: 6px 41px;
    border: 1px solid $hurricane;
    border-radius: 2px;
    transition: .25s;
    &:hover {
      background: $pink_lady;
    }
  }
  .journal__list {
    &--slider {
      display: inline-block;
      white-space: nowrap;
      .journal__item {
        outline: none;
        display: inline-block;
        vertical-align: middle;
        margin: 0 0;
        padding: 0;
        width: auto;
        border: none;
        position: relative;
        transition: all .30s;
        // margin-left: -80px;
        cursor: pointer;
        &:hover {
          .journal-item__link {
            &::after {
              background: transparent;
            }
          }
          transform: scale(1.1);
        }
        .journal-item {
          // width: 260px;
          display: flex;
          flex-direction: row;
          justify-content: center;
        }
        .journal-item__link {
          max-width: 180px;
          &::before {
            display: none;
          }
          &::after { 
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(255,255,255,.4);
            transition: .30s;
          }
          @include media('>=tablet') {
            &::after {
              background: rgba(246, 243, 241,.4);
            }
          }
        }
        .journal-item__overlay {
          display: none;
        }
        &--slide-active {
          transition: .30s;
          .journal-item__content {
            margin-right: 20px;
          }
          .journal-item__link {
            max-width: 260px;
            flex-wrap: nowrap;
            &::after {
              background: transparent;
            }
          }
          .journal-item__image {
            margin-bottom: 0;
            max-width: 100%;
          }
          .journal-item__info {
            display: none;
          }
          .links {
            display: flex;
          }
          &:hover {
            transform: scale(1);
          }
        }
      }
      .journal-item__content {
        margin-right: 0;
        flex-flow: column nowrap;
        align-items: flex-start;
      }
      .links {
        display: none;
      }
    }
  }
  .arrow-svg {
    stroke: $thunder;
  }
}

.slider {
  position: relative;
  &__nav {
    outline: none;
  }
  &__prev-wrapper, &__next-wrapper {
    position: absolute;
    top: 0;
    width: 100px;
    height: 100%;
    cursor: pointer;
    outline: none;
    z-index: 3;
    &:hover {
      .slider__prev-button {
        left: 20px;
      }
      .slider__next-button {
        right: 20px;
      }
    }
  }
  &__next-wrapper {
    right: 0px;
  }
  &__prev-button, &__next-button {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    border: none;
    border-radius: 2px;
    background: $thunder;
    padding: 5px 10px;
    cursor: pointer;
    transition: .30s;
    z-index: 2;
  }
  &__prev-button{
    left: -50px;
  }
  &__next-button {
    right: -50px;
  }
}

.arrow-left, .arrow-right {
  width: 10px;
  height: 18px;
}

.arrow-svg {
  width: 11px;
  height: 12px;
  stroke: $white;
  &--left {
    position: absolute;
    top: 50%;
    left: 12px;
    transform: rotate(-90deg) translate(50%, 0);
  }
}