.loader {
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: rgb(255,255,255);
  z-index: 9999;
  &__wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    span {
      display: inline-block;
      height: 15px;
      width: 15px;
      background: $crimson;
      border-radius: 0px;
      &:nth-child(1) {
        transform-origin: 0 50%;
        transform-perspective: 100px;
        animation: cuve 1s 0s infinite;
      }
      &:nth-child(2) {
        transform-origin: 0 50%;
        transform-perspective: 100px;
        animation: cuve 1s 0.5s infinite;
      }
      &:nth-child(3) {
        transform-origin: 0 50%;
        transform-perspective: 100px;
        animation: cuve 1s 1s infinite;
      }
      &:nth-child(4) {
        transform-origin: 0 50%;
        transform-perspective: 100px;
        animation: cuve 1s 1.5s infinite;
      }
      &:nth-child(5) {
        transform-origin: 0 50%;
        transform-perspective: 100px;
        animation: cuve 1s 2s infinite;
      }
    }
  }
}

@keyframes cuve {
  0% {
    transform: rotateY(-90deg) perspective(50px);
    background: $crimson_dark;
  }
  50% {
    transform: rotateY(0deg);
    background: $crimson;
  }
  100% {
    transform: rotateY(90deg) perspective(50px);
    transform-origin: 100% 50%;
    background: $crimson_dark;
  }
}
