.who-view {
  position: relative;
  &__wrapper {
    padding-bottom: 60px;
    @include media('>=desktop') {
      padding-bottom: 90px;
    }
  }
  &__main {
    background: $pampas;
    @include media('>=tablet') {
      background: transparent;
      padding-bottom: 30px;
    }
    .container {
      @include media('>=tablet') {
        max-width: 720px;
        padding: 0 20px;
      }
    }
  }
  &__top {
    position: relative;
    padding: 35px 0 20px;
    @include media('>=tablet') {
      position: static;
      padding-bottom: 30px;
    }
    &-wrapper {
      @include media('>=tablet') {
        padding: 0 30px
      }
    }
  }
  &__organization {
    color: $hurricane;
    font-size: 14px;
    line-height: 20px;
    &-name {
      color: $thunder;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  &__title {
    font-weight: 500;
    font-size: 30px;
    line-height: 40px;
    margin: 7px 0 10px;
    @include media('>=tablet') {
      font-weight: 600;
      font-size: 55px;
      line-height: 60px;
      margin: 7px 0 20px -3px;
    }
  }
  &__card {
    margin-bottom: 70px;
    @include media('>=tablet') {
      margin-bottom: 25px;
    }
  }
  &__social {
    margin-top: 10px;
    padding: 0 30px;
    @include media('>=desktop') {
      padding: 0;
    }
    .social {
      position: relative;
      display: block;
      @include media('>=desktop') {
        display: none;
      }
    }
  }
  &__categories {
    .categories__list {
      @include media('>=tablet') {
        padding: 0 30px
      } 
    }
  }
  .categories {
    margin-bottom: 10px;
    @include media('>=desktop') {
      margin-bottom: 0;
    }
  }
  .social {
    @include media('>=desktop') {
      position: absolute;
      top: 130px;
      right: 20px;
    }
    &__list {
      padding: 0;
      list-style-type: none;
      flex-direction: row;
      @include media('>=desktop') {
        flex-direction: column;
      }
    }
    &__item {
      @include media('>=desktop') {
        margin: 0 0 10px;
      }
    }
    &__link {
      @include media('>=desktop') {
        display: block;
      }
    }
    &__icon {
      @include media('>=desktop') {
        margin: 0 0 1px;
      }
    }
  }
  &--organization {
    .who-view__top {
      @include media('>=tablet') {
        padding-top: 65px;
      }
    }
  }
}

.card {
  display: flex;
  flex-direction: row;
  align-items: center;
  @include media('>=tablet') {
    border-bottom: 5px solid $thunder;
    padding-bottom: 40px;
  }
  &__image {
    margin-right: 20px;
    flex-shrink: 0;
    width: 100px;
    height: 100px;
    @include media('>=tablet') {
      margin-right: 30px;
      width: 200px;
      height: 200px;
    }
  }
  &__text {
    font-family: 'PT Serif';
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    @include media('>=tablet') {
      font-size: 25px;
      line-height: 35px;
    }
  }
}

.contact {
  position: relative;
  margin-bottom: 25px;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    width: 100vw;
    height: 100%;
    margin-left: -50vw;
    background: $white;
    display: block;
    border-top: 1px solid $swirl;
    border-bottom: 1px solid $swirl;
    z-index: 1;
  }
  @include media('>=tablet') {
    padding-top: 20px;
    &::before {
      display: none;
    }
  }
  &__wrapper {
    position: relative;
    padding: 23px 0;
    z-index: 2;
    @include media('>=tablet') {
      border-top: 5px solid $thunder;
      border-bottom: 5px solid $thunder;
      padding: 20px 30px;
    }
  }
  &__list {
    position: relative;
    z-index: 2;
  }
  &__item {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
    @include media('>=tablet') {
      margin-bottom: 15px;
    }
  }
  &__span {
    position: absolute;
    top: 8px;
    right: 0;
    align-self: flex-end;
    color: $hurricane;
    line-height: 20px;
    font-size: 14px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    z-index: 2;
    @include media('>=tablet') {
      color: $thunder;
      top: -25px;
    }
  }
  &__name, &__info {
    color: $thunder;
    font-family: 'PT Serif';
    font-weight: normal;
    font-size: 16px;
    line-height: 25px;
    @include media('>=tablet') {
      font-size: 18px;
      line-height: 30px;
    }
  }
}

.info-block {
  margin-bottom: 40px;
  @include media('>=tablet') {
    padding: 0 30px
  } 
  &__title {
    color: $thunder;
    font-weight: 500;
    line-height: 35px;
    font-size: 25px;
    margin-bottom: 5px;
  }
  &__text {
    font-family: 'PT Serif';
    font-weight: normal;
    color: $thunder;
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 15px;
  }
}
