.news-view {
  position: relative;
  &__social {
    @include media(">=desktop") {
      .social {
        position: absolute;
        top: 130px;
        height: 100%;
        right: 20px;
        display: block;
        margin-top: 0;
        &__list {
          flex-direction: column;
        }
        &__item {
          margin: 0 0 10px;
        }
        &__link {
          display: block;
        }
        &__icon {
          margin: 0 0 1px;
        }
      }
    }
  }
}

.preview-image {
  position: relative;
  width: 100%;
  height: 100%;
  margin-bottom: 20px;
  @include media(">=tablet") {
    margin-bottom: 40px;
  }
  @include media(">=desktop") {
    margin-bottom: 35px;
  }
  &__background-image {
    width: calc(100% + 40px);
    margin-left: -20px;
    height: 250px;
    @include media(">=tablet") {
      width: 100%;
      height: 400px;
      margin: 0;
    }
  }
  &__author {
    position: absolute;
    left: 0;
    bottom: -27px;
    display: block;
    color: $hurricane;
    line-height: 20px;
    font-size: 14px;
    display: none;
    @include media(">=tablet") {
      display: block;
    }
  }
  &--full-length {
    position: relative;
    left: 50%;
    width: 100vw;
    height: 250px;
    margin-left: -50vw;
    @include media(">=tablet") {
      height: 315px;
    }
    @include media(">=desktop") {
      height: 570px;
    }
    .preview-image__background-image {
      width: 100%;
      height: 100%;
      margin-left: 0;
    }
    .preview-image__author {
      left: 20px;
    }
  }
}
