.tags {
  overflow: hidden;
  &__wrapper {
    display: flex;
    flex-flow: row wrap;
  }
  &__content {
    display: grid;
    @include media('>=tablet') {
      grid-gap: 20px;
      margin-bottom: 15px;
      grid-template-columns: repeat(2, 280px);
    }
    @include media('>=desktop') {
      grid-template-columns: repeat(3, 280px);
    }
    @include media('>=big_desktop') {
      grid-template-columns: repeat(4, 280px);
    }
  }
  &__note {
    position: relative;
    width: calc(100% + 40px);
    margin-left: -20px;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      display: block;
      width: 100vw;
      margin-left: -50vw; 
      border-top: 1px solid $swirl;
    }
    @include media('>=tablet') {
      &::before {
        display: none;
      }
      width: 280px;
      margin-left: 0;
      grid-row: 1 / 3;
      grid-column: 2 / 3;
    }
    @include media('>=desktop') {
      grid-column: 3 / 4;
    }
    @include media('>=big_desktop') {
      width: 580px;
      grid-column: 3 / 5;
    }
  }
}
