  border: 1px solid #9F9093;
  border-radius: 2px;
  color: #231F20;
  font-family: "Fira Sans";
  font-size: 20px;
  line-height: 25px;
  background: transparent;
  margin: 0;
  cursor: pointer;
  transition: 0.25s;
  outline: none;
  white-space: nowrap;
  text-align: center;
}

.button--more {
