  background: #FFF3D7;
}

@media (min-width: 768px) {
  .button--more {
    width: auto;
  }
}

.button--send {
  color: #FFFFFF;
  font-size: 16px;
  background: #D63438;
  border-color: transparent;
  padding: 6px 98px;
  transition: 0.25s;
}

.button--send:hover {
  background: #FF292E;
}

.button--send:active {
  background: #CA161A;
}

.button--apply {
  display: block;
  width: 100%;
  padding: 2px 0;
  font-size: 14px;
  line-height: 25px;
}

.button--apply:hover {
  background-color: #FFF3D7;
}

.button--apply:active {
  background-color: #F6F3F1;
}

.button--expand {
  font-size: 16px;
  line-height: 25px;
  width: 100%;
  padding: 7px 0;
}

.button--expand:hover {
  background: #FFF3D7;
}

.button--expand:active {
  background-color: #F6F3F1;
}

.button--politics {
  border-color: #231F20;
  padding: 7px 45px;
  font-size: 16px;
  line-height: 25px;
  border-color: #918184;
}

.button--politics:hover {
  background: #FFF3D7;
}

.button-more {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 0);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
}

.button-up {
  position: absolute;
  top: -20px;
  right: 20px;
  background: #231F20;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 3px solid #FFFFFF;
  cursor: pointer;
  transition: 0.2s;
  z-index: 3;
}

.button-up-page--active .button-up {
  position: fixed;
  top: auto;
  bottom: 20px;
}

.filter-search {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  white-space: nowrap;
  margin-top: 30px;
}

.filter-search__list {
  position: relative;
  display: flex;
}

.filter-search__list::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 1px;
  height: 1px;
  width: 100%;
  display: flex;
  background: #918184;
}

.filter-search__item {
  padding: 0 10px;
  cursor: pointer;
  z-index: 3;
}

.filter-search__item:first-child {
  padding: 0 10px 0 0;
}

.filter-search__item:last-child {
  padding: 0 10px 0 0;
}

.filter-search__item--active .filter-search__name {
  color: #231F20;
}

.filter-search__item--active .filter-search__name::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-radius: 2px;
  transition: all .3s ease;
  border-bottom: 3px solid #D63438;
}

.filter-search__item--landscape {
  position: relative;
}

.filter-search__item--landscape::before {
  content: '';
  width: 100%;
  margin-left: -10px;
}

.filter-search__name {
  position: relative;
  color: #918184;
  display: block;
  line-height: 30px;
  padding: 0 10px;
  transition: .20s;
}

@media (min-width: 1024px) {
  .filter-search__name {
    max-width: 135px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
}

.filter-search__slide-line, .filter-search .filter-search__link::before {
  position: absolute;
  bottom: 0;
  height: 3px;
  border-radius: 2px;
  background-color: #D63438;
  transition: all .25s ease;
  z-index: 2;
}

.filter-search__more {
  margin-left: 30px;
}

.filter-search__more .more-svg {
  stroke: #918184;
  fill: transparent;
}

.filter-search__more .submenu {
  padding: 12px 0;
  cursor: pointer;
}

.filter-search__more .submenu__list {
  position: absolute;
  top: 30px;
  right: 0;
  visibility: hidden;
  border-radius: 2px;
  opacity: 0;
  box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.3);
  padding: 10px 0;
  background: #FFFFFF;
  z-index: 3;
}

.filter-search__more .submenu__item {
  color: #231F20;
  padding: 3px 20px;
}

.filter-search__more .submenu__item:hover {
  background: #F6F3F1;
}

@media (min-width: 1024px) {
  .filter-search__more .submenu__item .filter-search__name::after {
    display: none;
  }
}

.filter-search__more .submenu .filter-search__name {
  color: #231F20;
  padding: 0;
  max-width: 100%;
  text-overflow: unset;
  line-height: 25px;
  font-size: 16px;
}

.filter-search__select {
  display: none;
}

.dots__wrapper {
  display: flex;
  flex-direction: column;
}

.dots__line {
  width: 100%;
  height: 2px;
  background-image: url(../images/svg/line.svg);
}

.dots__line:first-child {
  margin-bottom: 7px;
}

