.loader {
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: white;
  z-index: 9999;
}

.loader__wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader__wrapper span {
  display: inline-block;
  height: 15px;
  width: 15px;
  background: #D63438;
  border-radius: 0px;
}

.loader__wrapper span:nth-child(1) {
  transform-origin: 0 50%;
  transform-perspective: 100px;
  animation: cuve 1s 0s infinite;
}

.loader__wrapper span:nth-child(2) {
  transform-origin: 0 50%;
  transform-perspective: 100px;
  animation: cuve 1s 0.5s infinite;
}

.loader__wrapper span:nth-child(3) {
  transform-origin: 0 50%;
  transform-perspective: 100px;
  animation: cuve 1s 1s infinite;
}

.loader__wrapper span:nth-child(4) {
  transform-origin: 0 50%;
  transform-perspective: 100px;
  animation: cuve 1s 1.5s infinite;
}

.loader__wrapper span:nth-child(5) {
  transform-origin: 0 50%;
  transform-perspective: 100px;
  animation: cuve 1s 2s infinite;
}

@keyframes cuve {
  0% {
    transform: rotateY(-90deg) perspective(50px);
    background: #982424;
  }
  50% {
    transform: rotateY(0deg);
    background: #D63438;
  }
  100% {
    transform: rotateY(90deg) perspective(50px);
    transform-origin: 100% 50%;
    background: #982424;
  }
}

