.journal {
  &__top-wrapper {
    overflow: hidden;
  }
  .page__wrapper-top {
    padding-bottom: 40px;
  }
  &__list {
    position: relative;
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    padding-bottom: 90px;
  }
  &__item {
    position: relative;
    margin: 0;
    width: 100%;
    &:hover {
      .journal-item__overlay {
        opacity: 1;
      }
      .journal-item__overlay-link {
        top: 0;
      }
    }
    @include media('>=tablet') {
      margin: 0 95px 60px 20px;
      padding: 0;
      border: none;
      width: auto;
      &:nth-of-type(2n) {
        margin-right: 75px;
      }
    }
    @include media('>=desktop') {
      &:nth-of-type(2n) {
        margin-right: 95px;
      }
      &:nth-of-type(3n) {
        margin-right: 75px;
      }
    }
    @include media('>=big_desktop') {
      &:nth-of-type(2n), &:nth-of-type(3n) {
        margin-right: 95px;
      }
      &:nth-of-type(4n) {
        margin-right: 75px;
      }
    }
  }
  &__button {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
}

.timeline {
  &__list {
    display: flex;
  }
  &__item {
    flex-shrink: 0;
    outline: none;
    background: $white;
    font-size: 16px;
    line-height: 25px;
    color: $thunder;
    border: 1px solid $dusty_gray;
    border-right: none;
    padding: 2px 8px;
    transition: .25s;
    cursor: pointer;
    background: transparent;
    &:first-child {
      border-radius: 2px 0 0 2px;
    }
    &:last-child {
      border-right: 1px solid $dusty_gray;
      border-radius: 0 2px 2px 0;
    }
    &--active {
      background: $pink_lady;
    }
    &:hover {
      background: $pink_lady;
    }
  }
  &__select {
    display: none;
  }
}
