//Plugins styles
@import "node_modules/include-media/dist/_include-media";
@import "./plugins/flatpickr/flatpickr";

// Settings styles
@import "./settings/fonts";
@import "./settings/variables";

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  margin: 0;
  min-width: 320px;
  font-family: "Fira Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.65;
  overflow-x: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  max-width: 420px;

  @include media(">=tablet") {
    max-width: 620px;
  }

  @include media(">=desktop") {
    max-width: 920px;
  }

  @include media(">=big_desktop") {
    max-width: 1220px;
  }
}

img {
  display: block;
  width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.wrapper-dk {
  position: relative;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 99;
  opacity: 0;
  visibility: hidden;
  background: rgba(35, 31, 32, 0.9);
  transition: 0.2s;
  &--visible {
    visibility: visible;
    opacity: 1;
  }
}

.page {
  &__wrapper {
    padding-bottom: 50px;
  }
  &__wrapper-top {
    padding: 25px 0 40px;
    background: $pampas;
    @include media(">=tablet") {
      padding: 95px 0 60px;
      background: transparent;
    }
  }
  &__title {
    color: $thunder;
    font-weight: 500;
    font-size: 30px;
    line-height: 40px;
    margin: 0 0 20px 0;
    @include media(">=tablet") {
      font-weight: 600;
      font-size: 70px;
      line-height: 80px;
      margin: 0 0 40px -3px;
    }
  }
}

.section {
  &__title-wrapper {
    position: relative;
    padding: 35px 0 20px;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      width: 100vw;
      height: 100%;
      margin-left: -50vw;
      background: $pampas;
      display: block;
      z-index: -1;
    }
    @include media(">=tablet") {
      &::before {
        display: none;
      }
    }
  }
  &__title-header {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    padding-bottom: 5px;
    &::after {
      content: "";
      position: absolute;
      left: -20px;
      bottom: 0;
      width: calc(100% + 40px);
      height: 5px;
      background: $thunder;
    }
  }
  &__title {
    color: $thunder;
    font-weight: 500;
    font-size: 21px;
    line-height: 30px;
    margin: 0;
    @include media(">=tablet") {
      font-weight: 600;
      font-size: 55px;
      line-height: 60px;
    }
  }
  &__popular {
    display: none;
    @include media(">=desktop") {
      display: block;
    }
  }
  &__content {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
  }
  &__item {
    width: 100%;
    @include media(">=tablet") {
      width: auto;
    }
  }
}

.close {
  position: absolute;
  top: 22px;
  right: 22px;
  width: 30px;
  height: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  &__line {
    width: 30px;
    height: 2px;
    &--mobile {
      width: 20px;
    }
  }
  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 30px;
    height: 2px;
    background: #fff;
  }
  &::before {
    transform: rotate(-45deg);
  }
  &::after {
    transform: rotate(45deg);
  }
  &--mobile {
    width: 20px;
    height: 20px;
    top: 20px;
    right: 20px;
    &::before,
    &::after {
      width: 20px;
      height: 1px;
      background: $hurricane;
    }
  }
  &--carousel-time,
  &--select {
    top: 15px;
    right: 15px;
    &::before,
    &::after {
      width: 20px;
      height: 1px;
      background: $hurricane;
    }
    @include media(">=tablet") {
      display: none;
    }
  }
}

.scroll-lock {
  position: fixed;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

._link {
  &:hover {
    text-decoration: underline;
  }
}

._live-internet-link {
  position: absolute;
  left: 0;
  bottom: 17px;
  display: block;
  @include media(">=desktop") {
    position: static;
    width: 88px;
    height: 15px;
    margin-top: 10px;
  }
}

