.popular {
  width: 100%;
  &__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  &__item {
    margin-right: 20px;
    &:last-child {
      margin-right: 0;
    }
    &:hover {
      .popular__link {
        color: $crimson;
      }
    }
  }
  &__link {
    white-space: nowrap;
    display: block;
    color: $thunder;
    font-weight: 500;
    font-size: 12px;
    line-height: 25px;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    transition: .30s;
  }
}