.item {
  position: relative;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    width: 100vw;
    margin-left: -50vw;
    display: block;
    border-top: 1px solid $swirl;
  }
  @include media(">=tablet") {
    &::before {
      display: none;
    }
  }
  &__figure {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 5px 0 20px;
    margin: 0;
    @include media(">=tablet") {
      padding: 0;
      width: 280px;
      height: 280px;
    }
  }
  &__figure-top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  &__category {
    position: relative;
    color: $thunder;
    font-weight: 500;
    font-size: 12px;
    line-height: 25px;
    letter-spacing: 1.1px;
    text-transform: uppercase;
    cursor: pointer;
    z-index: 5;
    max-width: 260px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    @include media(">=tablet") {
      max-width: 140px;
    }
    &:hover {
      text-decoration: underline;
      overflow: visible;
    }
  }
  &__time-wrapper {
    display: flex;
    flex-direction: row;
    @include media(">=tablet") {
      padding-right: 20px;
    }
    &--hide {
      display: none;
    }
  }
  &__time {
    color: $hurricane;
    font-size: 14px;
    line-height: 25px;
    margin-left: 5px;
    display: block;
  }
  &__time-icon {
    width: 12px;
    height: 12px;
  }
  &__image {
    position: relative;
    margin-bottom: 10px;
    height: 195px;
    overflow: hidden;
    @include media(">=tablet") {
      padding-right: 55px;
      height: 120px;
    }
    &--hide {
      display: none;
    }
    img {
      height: 100%;
    }
  }
  &__title {
    color: $thunder;
    font-weight: 500;
    font-size: 16px;
    line-height: 25px;
    margin: 0;
    &-link {
      color: $thunder;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  &__name-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
  &__release {
    display: flex;
    margin-top: 10px;
    &:hover {
      .item__span {
        text-decoration: underline;
      }
    }
  }
  &__span {
    color: $thunder;
    margin-left: 10px;
    display: block;
  }
  &__block-link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 4;
    &:hover {
      & + .item__figure .item__title {
        text-decoration: underline;
      }
    }
  }
}

.item--no-time {
  .item__figure {
    padding: 30px 0 20px;
    @include media(">=tablet") {
      padding: 0;
    }
  }
  .item__image {
    padding-right: 0;
    @include media(">=tablet") {
      height: 150px;
    }
  }
  .item__figure-top {
    position: absolute;
    top: 33px;
    left: 8px;
    z-index: 5;
    @include media(">=tablet") {
      top: 3px;
    }
    .item__category {
      color: $white;
    }
    .item__time-wrapper {
      display: none;
    }
  }
}

.item--no-img {
  .item__figure {
    padding: 15px 0;
    @include media(">=tablet") {
      padding: 0;
    }
  }
  .item__title {
    font-weight: 600;
    font-size: 21px;
    line-height: 30px;
  }
  .item__image {
    display: none;
  }
}

.item {
  &--news-day-small,
  &--news-day-large,
  &--promo {
    overflow: hidden;
    width: calc(100% + 40px);
    margin-left: -20px;
    .item__figure {
      padding: 0;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: 0.3s;
        background: rgba(35, 31, 32, 0.9);
      }
      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 1;
        background: linear-gradient(
          0deg,
          rgba(0, 0, 0, 0.5),
          rgba(0, 0, 0, 0.5)
        );
        transition: 0.3s;
      }
    }
    .item__category {
      position: absolute;
      top: 3px;
      left: 10px;
      color: $white;
    }
    .item__image {
      margin-bottom: 0;
      padding-right: 0;
      background: #000;
      width: 100%;
      height: 420px;
      @include media(">=tablet") {
        height: 280px;
      }
    }
    .item__title {
      color: $white;
      font-size: 25px;
      line-height: 35px;
    }
    .item__figcaption {
      position: absolute;
      right: 0;
      bottom: -20px;
      width: 100%;
      z-index: 3;
      transition: 0.3s;
      padding: 0 20px;
    }
    &:hover {
      .item__title {
        text-decoration: none;
      }
      .item__image {
        &::before {
          opacity: 1;
        }
        &::after {
          opacity: 0;
        }
      }
      .info-news {
        opacity: 1;
      }
      .item__figcaption {
        bottom: 20px;
        @include media(">=tablet") {
          bottom: 35px;
        }
      }
    }
    .item__block-link {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 4;
      &:hover {
        & + .item__figure .item__title {
          text-decoration: none;
        }
      }
    }
  }
}

.item--news-day-small {
  @include media(">=tablet") {
    width: auto;
    margin-left: 0;
  }
  .item__figure {
    max-height: 420px;
    padding: 0;
  }
  .item__figcaption {
    @include media(">=tablet") {
      bottom: -45px;
    }
  }
  &._no-time {
    .item__figcaption {
      @include media(">=tablet") {
        bottom: -25px;
      }
    }
  }
  &:hover {
    .item__figcaption {
      bottom: 20px;
    }
  }
  .item__image {
    margin-bottom: 0;
    padding-right: 0;
    height: 420px;
    @include media(">=tablet") {
      height: 280px;
    }
  }
  .item__title {
    @include media(">=tablet") {
      font-size: 16px;
      line-height: 25px;
    }
  }
  .item__span {
    color: $white;
    font-size: 14px;
    line-height: 20px;
    text-transform: uppercase;
    letter-spacing: 1.3px;
    margin-bottom: 10px;
    text-align: center;
    display: block;
  }
  .info-news {
    margin-top: 10px;
  }
  .info-news__item {
    margin-top: 2px;
  }
}

.item--news-day-large,
.item--news-day-large-no-img {
  @include media(">=tablet") {
    width: auto;
    margin-left: 0;
  }
  .item__figure {
    width: 100%;
    height: 100%;
    padding: 0;
    @include media(">=tablet") {
      width: 580px;
      height: 580px;
      padding: 0;
    }
  }
  .item__title {
    font-weight: 600;
    @include media(">=tablet") {
      font-size: 40px;
      line-height: 50px;
    }
  }
  .item__image {
    height: 420px;
    @include media(">=tablet") {
      margin-bottom: auto;
      width: 580px;
      height: 580px;
    }
  }
  .item__figcaption {
    padding: 0 20px;
    @include media(">=tablet") {
      bottom: -10px;
      padding: 0 40px;
    }
  }
}

.item--news-day-large-no-img {
  .item__figure {
    padding: 15px 0;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    @include media(">=tablet") {
      padding: 0;
    }
  }
  .item__figcaption {
    padding: 0;
    bottom: 0;
    width: 100%;
  }
  .item__category {
    margin-bottom: 5px;
    align-self: flex-end;
    @include media(">=tablet") {
      margin-bottom: 20px;
    }
  }
  .item__title {
    font-size: 25px;
    line-height: 35px;
    @include media(">=tablet") {
      font-size: 40px;
      line-height: 50px;
    }
  }
  .item__image {
    display: none;
  }
  .info-news {
    opacity: 1;
    &__span {
      color: $thunder;
    }
    .time-svg,
    .view-svg {
      fill: $thunder;
    }
    .timer-svg {
      stroke: $thunder;
      fill: transparent;
    }
  }
}

.info-news {
  margin-top: 15px;
  opacity: 0;
  transition: 0.3s;
  &__list {
    display: flex;
    flex-flow: row wrap;
  }
  &__item {
    margin-right: 30px;
    display: flex;
    flex-direction: row;
    align-items: center;
    &:last-child {
      margin: 0;
    }
  }
  &__span {
    color: $pampas;
    font-size: 14px;
    line-height: 20px;
    margin-left: 10px;
  }
  .time-svg {
    fill: $white;
  }
}

.item--media {
  .item__figure {
    width: 280px;
    height: 280px;
    padding: 0;
    @include media(">=tablet") {
      padding: 0;
    }
  }
  .item__image {
    padding-right: 0;
    @include media(">=tablet") {
      height: 150px;
    }
  }
  .item__category {
    position: absolute;
    top: 3px;
    left: 8px;
    color: $white;
    @include media(">=tablet") {
    }
  }
  .item__title {
    margin: 0 0 0 5px;
    color: $pampas;
  }
  .item__figcaption {
    display: flex;
  }
  .photo-svg,
  .video-svg {
    margin-right: 10px;
  }
  &-black {
    .item__title {
      color: $thunder;
    }
  }
}

.email-sub,
.read-number,
.subscribe-social {
  position: relative;
  border: 5px solid $thunder;
  width: 100%;
  height: 280px;
  @include media(">=tablet") {
    width: 280px;
  }
  &__wrapper {
    padding: 10px 15px 25px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  &__span {
    color: $thunder;
    font-weight: 500;
    font-size: 16px;
    line-height: 25px;
    margin-bottom: 12px;
    display: block;
  }
  &__input {
    color: $thunder;
    font-family: "Fira Sans";
    font-weight: normal;
    font-size: 16px;
    line-height: 25px;
    width: 100%;
    border: 1px solid $hurricane;
    background: $pampas;
    margin-bottom: 10px;
    padding: 7px 10px;
    border-radius: 2px;
    outline: none;
    transition: all 0.2s ease-out;
    &:focus {
      background: $white;
      border-color: $thunder;
    }
  }
  &__button {
    position: relative;
    display: block;
    background: $crimson;
    border: none;
    border-radius: 2px;
    font-family: "Fira Sans";
    font-weight: normal;
    font-size: 16px;
    line-height: 25px;
    padding: 8px 0;
    width: 100%;
    color: $white;
    cursor: pointer;
    margin: 0;
    text-align: center;
    transition: 0.25s;
    &:hover {
      background: $hover-button;
    }
    &:active {
      background: $active-button;
    }
  }
  &__dots-line {
    position: absolute;
    top: 10px;
    left: 0;
    width: 100%;
  }
  .dots {
    margin-left: 15px;
  }
  &__image {
    max-height: 168px;
    margin-bottom: 8px;
    img {
      width: auto;
      text-align: center;
      margin: 0 auto 0 auto;
    }
  }
  .social__icon {
    margin: 0;
    width: 36px;
    height: 36px;
  }
  .facebook-svg {
    width: 10px;
    height: 21px;
  }
  .vk-svg {
    width: 24px;
    height: 13px;
  }
  .ok-svg {
    width: 13px;
    height: 21px;
  }
}

.subscribe-social {
  &__wrapper {
    padding-bottom: 15px;
  }
  &__span {
    margin-bottom: 37px;
  }
}

.promo {
  position: relative;
  width: 100%;
  height: 420px;
  width: calc(100% + 40px);
  margin-left: -20px;
  cursor: pointer;
  &:hover {
    .item__figcaption {
      bottom: 25px;
      @include media(">=tablet") {
        bottom: 20px;
      }
    }
  }
  &--orange {
    .item__image {
      &::after {
        background: linear-gradient(
            0deg,
            rgba(255, 138, 0, 0.4),
            rgba(255, 138, 0, 0.4)
          ),
          linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
      }
    }
  }
  &--turquoise {
    .item__image {
      &::after {
        background: linear-gradient(
            0deg,
            rgba(0, 166, 188, 0.4),
            rgba(0, 166, 188, 0.4)
          ),
          linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
      }
    }
  }
  &--green {
    .item__image {
      &::after {
        background: linear-gradient(
            0deg,
            rgba(0, 146, 58, 0.4),
            rgba(0, 146, 58, 0.4)
          ),
          linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
      }
    }
  }
  &--red {
    .item__image {
      &::after {
        background: linear-gradient(
            0deg,
            rgba(209, 0, 0, 0.4),
            rgba(209, 0, 0, 0.4)
          ),
          linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
      }
    }
  }
  &--blue {
    .item__image {
      &::after {
        background: linear-gradient(
            0deg,
            rgba(43, 27, 224, 0.4),
            rgba(43, 27, 224, 0.4)
          ),
          linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
      }
    }
  }
  @include media(">=tablet") {
    width: 280px;
    height: 100%;
    margin-left: 0;
    width: auto;
  }
  .item__figcaption {
    padding: 0 20px;
    text-align: center;
    bottom: -20px;
    @include media(">=tablet") {
      bottom: -35px;
    }
  }
  .item__span {
    display: block;
    color: $white;
    font-size: 14px;
    line-height: 20px;
    text-transform: uppercase;
    letter-spacing: 1.1px;
    margin-bottom: 10px;
  }
  .item__title {
    color: $white;
    font-weight: 500;
    font-size: 25px;
    line-height: 35px;
    @include media(">=tablet") {
      font-size: 18px;
      line-height: 25px;
    }
  }
  .info-news__list {
    justify-content: center;
  }
  .info-news__item {
    @include media(">=tablet") {
      margin-right: 0;
      &:first-child {
        margin-right: 30px;
      }
    }
  }
  .info-news {
    margin-top: 13px;
  }
  .info-news__item {
    margin-top: 2px;
  }
}

.journal-item {
  position: relative;
  color: $thunder;
  &__wrapper {
    position: relative;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    width: 100%;
    padding: 20px 0;
    cursor: pointer;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      width: 100vw;
      margin-left: -50vw;
      border-top: 1px solid $swirl;
      display: block;
    }
    @include media(">=tablet") {
      max-width: 185px;
      padding: 0;
      &::before {
        display: none;
      }
    }
  }
  &__content {
    position: relative;
    margin-bottom: 15px;
    @include media(">=tablet") {
      flex-flow: column nowrap;
      align-items: flex-start;
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    order: 2;
    @include media(">=tablet") {
      order: 3;
    }
  }
  &__image {
    position: relative;
    margin: 0 10px 0 0;
    order: 1;
    height: 180px;
    width: 135px;
    @include media(">=tablet") {
      margin: 0 20px 15px 0;
    }
  }
  &__img {
    background-color: $pink_lady;
    height: 100%;
  }
  &__number {
    color: $thunder;
    display: block;
  }
  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: 0.25s;
  }
  &__overlay-link {
    position: relative;
    top: 10px;
    color: $pampas;
    font-size: 14px;
    line-height: 25px;
    border: 1px solid $pampas;
    padding: 2px 25px;
    border-radius: 2px;
    transition: 0.3s;
  }
  &__button {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
}

.links {
  display: flex;
  justify-content: center;
  flex-direction: column;
  order: 3;
  height: inherit;
  z-index: 5;
  @include media(">=tablet") {
    order: 2;
  }
  &__wrapper {
    border: 1px solid $hurricane;
    border-radius: 2px;
    width: 30px;
  }
  &__link {
    position: relative;
    width: 100%;
    height: 30px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: 0.25s;
    &:last-child {
      border-top: 1px solid $hurricane;
    }
    &:hover {
      background: $pink_lady;
    }
  }
}

.person,
.organizations {
  .item__figure {
    padding: 5px 0 10px;
    @include media(">=tablet") {
      padding: 0;
    }
  }
  .item__image {
    width: 120px;
    height: 120px;
    margin: 0;
    padding: 0;
    flex-shrink: 0;
  }
  .item__title-name {
    display: block;
    @include media(">=tablet") {
      word-spacing: 60px;
    }
  }
  .item__title {
    margin-right: 15px;
  }
  .item__title-link {
    display: block;
  }
}

.organizations {
  .item__title {
    max-width: 230px;
    @include media(">=tablet") {
      max-width: 130px;
    }
  }
}

.item--article-top {
  &::before {
    display: none;
  }
  .info-news__list {
    margin-bottom: 7px;
  }
  .item__title {
    color: $thunder;
    font-weight: 500;
    font-size: 30px;
    line-height: 40px;
    &:hover {
      text-decoration: none;
    }
    @include media(">=tablet") {
      font-weight: bold;
      font-size: 55px;
      line-height: 60px;
    }
  }
  .info-news__item {
    &:last-child {
      margin: 5px 0 0;
    }
    @include media(">=tablet") {
      &:last-child {
        margin: 0;
      }
    }
  }
  .info-news__span {
    color: $hurricane;
  }
  .time-svg,
  .view-svg {
    fill: $hurricane;
  }
  .timer-svg {
    fill: transparent;
    stroke: $hurricane;
  }
}

.categories {
  &__list {
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
    padding: 0;
  }
  &__item {
    margin-right: 20px;
    &:last-child {
      margin-right: 0;
    }
  }
  &__category {
    color: $thunder;
    font-weight: 500;
    line-height: 25px;
    font-size: 12px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    cursor: default;
    &:hover {
      cursor: pointer;
      text-decoration: underline;
    }
  }
}

.employee {
  max-width: 380px;
  display: flex;
  &__image {
    width: 100px;
    height: 100px;
    margin-right: 20px;
    overflow: hidden;
  }
  &__content {
    max-width: 260px;
    display: flex;
    flex-direction: column;
  }
  &__name {
    color: $thunder;
    font-weight: 500;
    margin: 0;
  }
  &__span {
    display: block;
    color: $hurricane;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 30px;
  }
  &__info-wrapper {
    & > a + a {
      display: block;
    }
  }
  &__info-name {
    color: $hurricane;
    margin-right: 5px;
  }
  &__info-name,
  &__info {
    font-size: 14px;
    line-height: 20px;
  }
  &__info {
    color: $thunder;
  }
  &__list {
    margin-bottom: 5px;
  }
  .social__icon {
    width: 20px;
    height: 20px;
  }
}

.pdf-svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: 5px;
  width: 17px;
  height: 18px;
}
.arrow-down-svg {
  width: 11px;
  height: 12px;
}

.timer-svg {
  width: 12px;
  height: 14px;
  fill: transparent;
  stroke: $white;
}

.view-svg {
  width: 21px;
  height: 12px;
  fill: $white;
}

.time-svg {
  width: 12px;
  height: 12px;
  fill: $hurricane;
}

.date-svg {
  width: 24px;
  height: 24px;
}

.video-svg,
.photo-svg {
  width: 14px;
  height: 9px;
}

.user-svg {
  width: 100px;
  height: 100px;
}

.full-row {
  .item__image {
    @include media(">=tablet") {
      width: 100%;
      height: 580px;
      padding: 0;
    }
  }
}

.object-center {
  object-fit: cover;
  object-position: center center;
}

.object-up {
  object-fit: cover;
  object-position: center top;
}

.object-down {
  object-fit: cover;
  object-position: center bottom;
}

.object-embed {
  object-fit: contain;
  object-position: center center;
}
