.who {
  overflow: hidden;
  .page__title {
    margin-bottom: 15px;
    @include media('>=tablet') {
      margin-bottom: 25px;
    }
  }
  .page__wrapper-top {
    padding-bottom: 22px;
    @include media('>=tablet') {
      padding-bottom: 40px;
    }
  }
  &__content-top {
    background: $pampas;
    padding-bottom: 22px;
    @include media('>=tablet') {
      background: transparent;
      padding-bottom: 40px;
    }
  }
  &__personal-form {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    @include media('>=desktop') {
      flex-wrap: nowrap;
    }
  }
  .search-field {
    width: 100%;
    margin-bottom: 22px;
    @include media('>=tablet') {
      width: 380px;
      margin-bottom: 35px;
    }
    @include media('>=desktop') {
      margin-bottom: 0;
    }
  }
  &__button {
    width: 100%;
    font-size: 16px;
    line-height: 25px;
    border-radius: 2px;
    padding: 3px 23px;
    text-align: center;
    white-space: nowrap;
    &:nth-child(2){
      border-radius: 2px 0 0 2px;
    }
    &:last-child {
      border-left: 0;
      border-radius: 0 2px 2px 0;
    }
    &:hover, &--active {
      background: $pink_lady;
    }
  }
  &__filters {
    width: 100%;
    display: flex;
    @include media('>=tablet') {
      width: auto;
    }
  }
  &__content {
    display: block;
    height: 0;
    overflow: hidden;
    &--active {
      height: 100%;
    }
  }
  .filter-search {
    margin-top: 0;
  }
  &__select {
    display: none;
  }
}

.tabs {
  display: flex;
  &__button {
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    color: $hurricane;
    border: 0;
    padding: 4px 9px 1px;
    transition: .25s;
    &:first-child {
      margin-right: 9px;
    }
    &:hover, &--active {
      background: $thunder;
      color: $pampas;
    }
  }
}

.person, .organizations {
  &__wrapper {
    padding-right: 10px;
  }
  &__list {
    position: relative;
    display: flex;
    flex-flow: row wrap;
    padding-bottom: 90px;
  }
  &__item {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: calc(100% + 40px);
      margin-left: -20px;
      border-top: 1px solid $swirl;
    }
    @include media('>=tablet') {
      &::before {
        display: none;
      }
      padding: 0;
      margin: 0 20px 20px 0;
      &:nth-of-type(2n) {
        margin-right: 0;
      }
    }
    @include media('>=desktop') {
      &:nth-of-type(2n) {
        margin-right: 20px;
      }
      &:nth-of-type(3n) {
        margin-right: 0;
      }
    }
    @include media('>=big_desktop') {
      &:nth-of-type(2n), &:nth-of-type(3n) {
        margin-right: 20px;
      }
      &:nth-of-type(4n) {
        margin-right: 0;
      }
    }
  }
}

.organizations {
  .arrow-right-small-svg {
    margin-top: 5px;
  }
}

.arrow-right-small-svg {
  width: 16px;
  height: 15px;
}