.section-dots {
  position: relative;
  width: 100%;
  &__title-wrapper {
    position: relative;
    padding: 45px 0 20px;
  }
  &__title-header {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    padding-bottom: 5px;
    &::after {
      content: "";
      position: absolute;
      left: -10px;
      bottom: 0;
      width: calc(100% + 20px);
      height: 5px;
      background: $thunder;
    }
    @include media(">=tablet") {
      &::after {
        left: -20px;
        width: calc(100% + 40px);
      }
    }
  }
  &__title {
    color: $thunder;
    font-weight: 500;
    font-size: 21px;
    line-height: 30px;
    margin: 0;
    @include media(">=tablet") {
      font-weight: bold;
      font-size: 55px;
      line-height: 60px;
    }
  }
  &__right {
    display: none;
    margin-left: auto;
    @include media(">=desktop") {
      display: block;
    }
  }
  &__news-day {
    display: none;
    @include media(">=tablet") {
      display: block;
    }
  }
  &__item {
    width: 100%;
  }
  &__time {
    color: $hurricane;
    font-size: 14px;
    line-height: 25px;
    margin-left: 5px;
    display: block;
  }
}
