.special-projects {
  background: $pampas;
  @include media('>=tablet') {
    background: transparent;
  }
  .page__wrapper {
    padding-bottom: 80px;
  }
  .page__title {
    margin: 0;
  }
  .item__figcaption {
    text-align: center;
  }
  .info-news__list {
    justify-content: center;
  }
  .template__list {
    gap: 10px;
    @include media('>=tablet') {
      gap: 20px;
    }
  }
  .promo {
    .item__text {
      font-size: 18px;
    }
  }
}
