.filter-search {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  white-space: nowrap;
  margin-top: 30px;
  &__list {
    position: relative;
    display: flex;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 1px;
      height: 1px;
      width: 100%;
      display: flex; 
      background: $hurricane;
    }
  }
  &__item {
    padding: 0 10px;
    cursor: pointer;
    z-index: 3;
    &:first-child {
      padding: 0 10px 0 0;
    }
    &:last-child {
      padding: 0 10px 0 0;
    }
    &--active {
      .filter-search__name {
        color: $thunder;
      }
    }
    &--active {
      .filter-search__name {
        &::after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          border-radius: 2px;
          transition: all .3s ease;
          border-bottom: 3px solid $crimson;
        }
      }
    }
    &--landscape {
      position: relative;
      &::before {
        content: '';
        width: 100%;
        margin-left: -10px;
      }
    }
  }
  &__name {
    position: relative;
    color: $hurricane;
    display: block;
    line-height: 30px;
    padding: 0 10px;
    transition: .20s;
    @include media('>=desktop') {
      max-width: 135px;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
  &__slide-line, .filter-search__link::before {
    position: absolute;
    bottom: 0;
    height: 3px;
    border-radius: 2px;
    background-color: $crimson;
    transition: all .25s ease;
    z-index: 2;
  }
  &__more {
    margin-left: 30px;
    .more-svg {
      stroke: $hurricane;
      fill: transparent;
    }
    .submenu {
      padding: 12px 0;
      cursor: pointer;
      &__list {
        position: absolute;
        top: 30px;
        right: 0;
        visibility: hidden;
        border-radius: 2px;
        opacity: 0;
        box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.3);
        padding: 10px 0;
        background: $white;
        z-index: 3;
      }
      &__item {
        color: $thunder;
        padding: 3px 20px;
        &:hover {
          background: $pampas; 
        }
        @include media('>=desktop'){
          .filter-search__name {
            &::after {
              display: none;
            }
          }
        }
      }
      .filter-search__name {
        color: $thunder; 
        padding: 0;
        max-width: 100%;
        text-overflow: unset;
        line-height: 25px;
        font-size: 16px;
      }
    }
  }
  &__select {
    display: none;
  }
}
