.exchange-rates {
  display: flex;
  &__item {
    display: flex;
    flex-direction: row;
    align-items: center;
    &:first-child {
      margin-right: 25px;
    }
  }
  &__count {
    color: $white;
    font-size: 14px;
    line-height: 20px;
    margin-right: 3px;
  }
  &__currency {
    font-style: normal;
    margin-right: 3px;
  }
}

.home {
  &__header {
    position: relative;
    background: url(../images/home.jpg) no-repeat;
    background-size: cover;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
    }
  }
  &__header-wrapper {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    padding: 120px 0 25px;
    @include media('>=tablet') {
      padding: 150px 0 40px;
    }
    @include media('>=desktop') {
      // height: 660px;
      padding: 75px 0;
    }
  }
  &__exchange-rates {
    position: absolute;
    top: 15px;
    right: 0;
    display: none;
    @include media('>=tablet') {
      display: flex;
    }
    @include media('>=desktop') {
      top: 37px;
      display: flex;
    }
  }
  &__news-feed-wrapper {
    position: relative;
    align-self: flex-start;
    .news-feed__link {
      color: $white;
    }
    .news-feed__more {
      color: $white;
      border-color: $white;
      &:hover {
        color: $crimson;
        border-color: $crimson;
      }
    }
  }
  &__news-feed {
    display: none;
    @include media('>=desktop') {
      display: block;
    }
  }
  .actual__title-wrapper {
    @include media('>=desktop') {
      padding-top: 35px;
    }
  }
  &__news-feed-content {
    display: block;
    .news-feed {
      width: 100%;
      padding: 20px 0 10px;
      @include media('>=tablet') {
        padding-top: 40px;
      }
      &__more {
        color: $crimson;
      }
    }
    @include media('>=desktop') {
      display: none;
    }
  }
  &__media-section {
    margin-top: 35px;
  }
}

.home-news {
  max-width: 580px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  @include media('>=big_desktop') {
    max-width: 830px;
  }
  &__category {
    font-weight: 500;
    font-size: 12px;
    line-height: 25px;
    letter-spacing: 1.1px;
    text-transform: uppercase;
    display: inline-block;
    color: $pampas;
    cursor: pointer;
    &:hover {
      text-decoration: underline;
    }
    @include media('>=tablet') {
      margin-bottom: 5px;
    }
    @include media('>=desktop') {
      margin-bottom: 8px;
    }
    @include media('>=big_desktop') {
      margin-bottom: 12px;
    }
  }
  &__title {
    color: $pampas;
    font-weight: 500;
    font-size: 30px;
    line-height: 40px;
    margin: 0;
    &:hover {
      text-decoration: underline;
    }
    @include media('>=tablet') {
      font-weight: 600;
      font-size: 40px;
      line-height: 50px;
      margin-left: -2px;
    }
    @include media('>=big_desktop') {
      font-size: 40px;
      line-height: 50px;
      margin-left: -4px;
    }
  }
}

.exchange-rates-up-svg, .exchange-rates-down-svg {
  width: 8px;
  height: 10px;
}