.button {
  display: inline-block;
  border: 1px solid $dusty_gray;
  border-radius: 2px;
  color: $thunder;
  font-family: "Fira Sans";
  font-size: 20px;
  line-height: 25px;
  background: transparent;
  margin: 0;
  cursor: pointer;
  transition: 0.25s;
  outline: none;
  white-space: nowrap;
  text-align: center;
  &--more {
    padding: 11px 70px;
    width: 100%;
    height: 50px;
    &:hover {
      background: $pink_lady;
    }
    @include media(">=tablet") {
      width: auto;
    }
  }

  &--send {
    color: $white;
    font-size: 16px;
    background: $crimson;
    border-color: transparent;
    padding: 6px 98px;
    transition: 0.25s;
    &:hover {
      background: $hover-button;
    }
    &:active {
      background: $active-button;
    }
  }
  &--apply {
    display: block;
    width: 100%;
    padding: 2px 0;
    font-size: 14px;
    line-height: 25px;
    &:hover {
      background-color: $pink_lady;
    }
    &:active {
      background-color: $pampas;
    }
  }
  &--expand {
    font-size: 16px;
    line-height: 25px;
    width: 100%;
    padding: 7px 0;
    &:hover {
      background: $pink_lady;
    }
    &:active {
      background-color: $pampas;
    }
  }
  &--politics {
    border-color: $thunder;
    padding: 7px 45px;
    font-size: 16px;
    line-height: 25px;
    border-color: $hurricane;
    &:hover {
      background: $pink_lady;
    }
  }
}

.button-more {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 0);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
}

.button-up {
  position: absolute;
  top: -20px;
  right: 20px;
  background: $thunder;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 3px solid $white;
  cursor: pointer;
  transition: 0.2s;
  z-index: 3;
  &-page {
    &--active {
      .button-up {
        position: fixed;
        top: auto;
        bottom: 20px;
      }
    }
  }
}
