.journal-view {
  &__top-wrapper {
    background: $pampas;
    padding: 35px 0 40px 0;
    @include media('>=tablet') {
      background: transparent;
    }
  }
  &__title {
    color: $thunder;
    font-weight: 500;
    font-size: 25px;
    line-height: 30px;
    margin:0 0 25px 0;
    @include media('>=tablet') {
      font-weight: 600;
      font-size: 70px;
      line-height: 80px;
      margin-bottom: 10px;
      margin-left: -6px;
    }
  }
  &__list {
    display: flex;
    flex-flow: row wrap;
  }
  &__item {
    position: relative;
    @include media('>=tablet') {
      padding: 0;
      margin: 0 20px 40px 0;
      &:nth-of-type(2n) {
        margin-right: 0;
      }
    }
    @include media('>=desktop') {
      &:nth-of-type(2n) {
        margin-right: 20px;
      }
      &:nth-of-type(3n) {
        margin-right: 0;
      }
    }
    @include media('>=big_desktop') {
      &:nth-of-type(2n), &:nth-of-type(3n) {
        margin-right: 20px;
      }
      &:nth-of-type(4n) {
        margin-right: 0;
      }
    }
  }
}