.social {
  &__wrapper {
    &--fixed {
      position: fixed;
      top: 30px;
      right: inherit;
    }
  }
  &__icon {
    position: relative;
    z-index: 5;
  }
  &__list {
    display: flex;
  }
  &__item {
    margin-right: 10px;
    &:last-child {
      margin-right: 0;
    }
  }
  &__link {
    position: relative;
    display: flex;
    &--vk > div {
      background: $vk;
    }
    &--ok > div {
      background: $ok;
    }
    &--facebook > div {
      background: $facebook;
    }
  }
  &__icon, &__count {
    width: 30px;
    height: 30px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  &__icon {
    position: relative;
    margin-right: 1px;
    z-index: 2;
  }
  &__count {
    color: $white;
    font-family: "SourceSansProRegular";
    font-size: 12px;
    line-height: 20px;
    background: inherit;
    transition: all .30s;
  }
}

.facebook-svg {
  width: 6px;
  height: 13px;
}
.ok-svg {
  width: 8px;
  height: 13px;
}
.vk-svg {
  width: 14px;
  height: 8px;
}