.projects-view {
  &__header {
    position: relative;
    padding: 45px 0 50px 0;
    background: url(../images/special-project.jpg);
    background-size: cover;
    margin-bottom: 40px;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
    }
    @include media('>=tablet') {
      margin-bottom: 50px;
      padding: 160px 0 265px 0;
    }
    @include media('>=desktop') {
      padding-bottom: 210px;
    }
  }
  &__info {
    position: relative;
    display: block;
    z-index: 3;
  }
  &__category {
    display: block;
    color: $white;
    font-size: 14px;
    line-height: 20px;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    margin-bottom: 35px;
    @include media('>=desktop') {
      margin-bottom: 15px;
    }
  }
  &__title {
    font-weight: 500;
    color: $pampas;
    line-height: 40px;
    font-size: 30px;
    margin: 0;
    @include media('>=tablet') {
      font-weight: 600;
      line-height: 50px;
      font-size: 40px;
      margin: 0 0 15px -2px;
    }
    @include media('>=desktop') {
      line-height: 80px;
      font-size: 70px;
      margin: 0 0 30px -3px;
    }
  }
  &__text {
    color: $pampas; 
    font-family: 'PT Serif';
    font-weight: normal;
    line-height: 30px;
    font-size: 18px;
    @include media('>=tablet') {
      line-height: 35px;
      font-size: 22px;
    }
  }
}