.template {
  position: relative;
  padding-bottom: 70px;
  display: grid;
  grid-gap: 20px;
  grid-auto-rows: auto;
  grid-template-columns: repeat(1, 1fr);
  @include media('>=tablet') {
    grid-auto-rows: 280px;
    grid-template-columns: repeat(2, 280px);
  }
  
  @include media('>=desktop') {
    grid-template-columns: repeat(3, 280px);
  }

  @include media('>=big_desktop') {
    grid-template-columns: repeat(4, 280px);
  }
  &__wrapper {
    position: relative;
    overflow: hidden;
  }
  &__item {
    height: 100%;
  }
  &--six_blocks_one_column {
    .template__item {
      &:nth-of-type(4) {
        @include media('>=tablet') {
          grid-row: 1 / 3;
          grid-column: 2 / 3; 
        }
        @include media('>=desktop') {
          grid-row: 1 / 3;
          grid-column: 3 / 4; 
        }
        @include media('>=big_desktop') {
          grid-row: 1 / 3;
          grid-column: 4 / 5;
        }
      }
    }
  }
  &--one_important_four_blocks, &--one_important_eight_blocks {
    .template__item {
      &:nth-of-type(1) {
        @include media('>=tablet') {
          grid-row: 1 / 3;
          grid-column: 1 / 3;
        }
      }
    }
  }
  &--one_important_six_blocks {
    .template__item {
      &:nth-of-type(1) {
        display: block;
        @include media('>=tablet') {
          grid-row: 1 / 3;
          grid-column: 1 / 3;
          display: none;
        }
        @include media('>=desktop') {
          display: block;
        }
      }
      &:nth-of-type(4) {
        @include media('>=tablet') {
          grid-row: 1 / 3;
          grid-column: 2 / 3; 
        }
        @include media('>=desktop') {
          grid-row: 1 / 3;
          grid-column: 3 / 4; 
        }
        @include media('>=big_desktop') {
          grid-row: 1 / 3;
          grid-column: 4 / 5;
        }
      }
    }
  }
  &--three_blocks_two_columns {
    background: $thunder;;
    .template__wrapper {
      padding: 40px 0;
      overflow: hidden;
      @include media('>=tablet') {
        padding: 50px 0;
      }
    }
    .template__title {
      position: relative;
      color: $pampas;
      font-weight: bold;
      font-size: 21px;
      line-height: 30px;
      margin: 0 0 30px 0;
      z-index: 3;
      @include media('>=tablet') {
        margin-bottom: 40px;
        font-size: 55px;
        line-height: 60px;
      }
    }
    .template__list {
      display: flex;
    }
    .template__item {
      padding-right: 20px;
    }
    .item__figure {
      &::before {
        display: none;
      }
    }
    .item--media {
      &::before {
        display: none;
      }
    }
    .slider__prev-wrapper {
      background: linear-gradient(to right, rgba(35, 31, 32, 1),rgba(35, 31, 32, 0) );
      z-index: 2;
    }
    .slider__next-wrapper {
      background: linear-gradient(to left, rgba(35, 31, 32, 1),rgba(35, 31, 32, 0) );
      z-index: 2;
    }
  }
  &--banner {
    .template__item {
      &:last-child {
        @include media('>=tablet') {
          grid-row: 7;
          grid-column: 1 / 3;
        }
        @include media('>=desktop') {
          grid-row: 5;
          grid-column: 1 / 4;
        }
        @include media('>=big_desktop') {
          grid-row: 4;
          grid-column: 1 / 5;
        }
      }
    }
  }
  &--flex {
    justify-content: flex-start;
    flex-flow: row wrap;
    & > * {
      margin-bottom: 20px;
      @include media('>=tablet') {
        margin-right: 20px;
        &:nth-of-type(2n) {
          margin-right: 0;
        }
      }
      @include media('>=desktop') {
        &:nth-of-type(2n) {
          margin-right: 20px;
        }
        &:nth-of-type(3n) {
          margin-right: 0;
        }
      }
      @include media('>=big_desktop') {
        &:nth-of-type(2n), &:nth-of-type(3n) {
          margin-right: 20px;
        }
        &:nth-of-type(4n) {
          margin-right: 0;
        }
      }
    }
  }
}

.full-row {
  margin-bottom: 20px;
  @include media('>=tablet') {
    flex: 1 0 100%;
    .item__figure, .banner--square-big {
      width: 100%;
    }
  }
  @include media('>=big_desktop') {
    .item__figure, .banner--square-big {
      height: 280px;
    }
  }
}
