.search-page {
  overflow: hidden;
  &__top-wrapper {
    padding: 40px 0 30px;
    background: $pampas;
    @include media('>=tablet') {
      background: transparent;
      padding: 50px 0 35px;
    }
  }
  &__input-wrapper {
    max-width: 100%;
    @include media('>=desktop') {
      max-width: 470px;
    }
  }
  &__timeline {
    margin-bottom: 25px;
    @include media('>=tablet') {
      margin-bottom: 35px;
    }
  }
  &__title {
    font-weight: 500;
    font-size: 25px;
    line-height: 30px;
    color: $thunder;
    margin: 0 0 20px 0;
    @include media('>=tablet') {
      font-weight: 600;
      font-size: 70px;
      line-height: 80px;
      margin: 0 0 30px -4px;
    }
  }
  &__list {
    display: flex;
    flex-flow: row wrap;
    @include media('>=tablet') {
      margin-bottom: 50px;
    }
  }
  &__item {
    @include media('>=tablet') {
      padding: 0;
      margin: 0 20px 40px 0;
      &:nth-of-type(2n) {
        margin-right: 0;
      }
    }
    @include media('>=desktop') {
      &:nth-of-type(2n) {
        margin-right: 20px;
      }
      &:nth-of-type(3n) {
        margin-right: 0;
      }
    }
    @include media('>=big_desktop') {
      &:nth-of-type(2n), &:nth-of-type(3n) {
        margin-right: 20px;
      }
      &:nth-of-type(4n) {
        margin-right: 0;
      }
    }
  }
  .timeline {
    &--search-page {
      display: flex;
      overflow-x: visible;
      width: auto;
      margin-left: 0;
      left: 0;
      padding: 0;
      @include media('>=tablet') {
        display: flex;
      }
    }
    &__list {
      flex-direction: column;
      justify-content: space-between;
      @include media('>=tablet') {
        flex-wrap: nowrap;
        flex-direction: row;
      }
    }
    &__item {
      position: relative;
      color: $thunder;
      font-weight: normal;
      line-height: 25px;
      font-size: 16px;
      margin-bottom: 7px;
      white-space: nowrap;
      cursor: pointer;
      &:last-child {
        margin-bottom: 0;
      }
      @include media('>=tablet') {
        &:first-child {
          text-transform: none;
        }
        &:last-child {
          display: none;
        }
        text-transform: lowercase;
        margin-bottom: 0;
        &:nth-child(-n+3) {
          width: auto;
          text-align: center;
        }
        width: auto;
      }
      @include media('>=desktop') {
        &:nth-child(-n+3) {
          text-align: left;
        }
      }
      &--button {
        &:nth-child(1) {
          border-radius: 2px 0 0 2px;
        }
        @include media('>=tablet') {
          padding: 1.5px 6px;
          border: 1px solid $dusty_gray;
          border-right: 0;
        }
      }
      &--active {
        background: transparent;
        @include media('>=tablet') {
          background: $pink_lady;
        }
        .timeline__span-checkbox {
          &::before {
            opacity: 1;
          }
        }
      }
    }
    &__span-checkbox {
      display: inline-block;
      vertical-align: middle;
      margin-right: 10px;
      width: 16px;
      height: 16px;
      cursor: pointer;
      border-radius: 50%;
      position: relative;
      border: 1px solid $thunder;
      &::before {
        content: "";        
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: $crimson;
        transition: opacity .20s;
      }
      @include media('>=tablet') {
        display: none;
      }
    }
    &__input {
      position: relative;
      color: $thunder;
      font-family: 'Fira Sans';
      font-weight: normal;
      line-height: 25px;
      font-size: 16px;
      width: 119px;
      height: 100%;
      max-height: 30px;
      padding: 0 5px;
      margin: 0;
      border: 1px solid $dusty_gray;
      background: url(../images/svg/date.svg) 95% 50% no-repeat;
      background-color: $white;
      &::placeholder {
        color: $thunder;
      }
      &--from {
        border-radius: 2px 0 0 2px;
        border-right: 0;
        @include media('>=tablet') {
          border-radius: 0;
          border-left: 1px solid $dusty_gray;
        }
      }
      &--to {
        border-radius: 0 2px 2px 0;
      }
      @include media('>=tablet') {
        width: 171px;
        background-color: transparent;
        &--from {
          border-radius: 0;
        } 
      }
      @include media('>=desktop') {
        width: 116px;
      }
    }
  }
  &__field {
    margin-bottom: 20px;
  }
  &__button-filters {
    position: relative;
    padding: 0;
    cursor: default;
    outline: none;
    background: #FFFFFF;
    border-color: $dusty_gray;
    @include media('>=tablet') {
      display: none;
    }
  }
  &__input-mobile {
    width: 100%;
    display: none;
    &--visible {
      display: flex;
      @include media('>=tablet') {
        background: $pink_lady;
      }
    }
    @include media('>=tablet') {
      display: flex;
    }
  }
  &__mobile {
    position: fixed;
    left: 0;
    bottom: 0;
    left: 50%;
    margin-left: -50vw;
    width: 100vw;
    background: $white;
    padding: 20px;
    transition: .20s;
    z-index: 999;
    @include media('>=tablet') {
      position: relative;
      left: auto;
      margin-left: 0;
      width: 100%;
      opacity: 1;
      padding: 0;
      z-index: auto;
    }
    &--hide {
      bottom: -100%;
      opacity: 0;
      @include media('>=tablet') {
        bottom: auto;
        opacity: 1;
      }
    }
    &-span {
      display: block;
      font-weight: 500;
      margin-bottom: 15px;
      @include media('>=tablet') {
        display: none;
      }
    }
  }
  &__selected {
    position: relative;
    color: $thunder;
    font-size: 16px;
    line-height: 25px;
    text-align: left;
    height: 100%;
    background: transparent;
    outline: none;
    cursor: pointer;
    display: block;
    padding: 1px 73px 1px 10px;
    z-index: 3;
  }
  &__nothing {
    color: $thunder;
    font-weight: 500;
    font-size: 25px;
    line-height: 35px;
    margin-bottom: 90px;
  }
}

.select-date {
  display: none;
}

.flatpickr-calendar {
  right: 0;
  background: #FFFFFF;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  width: 236px;
  margin-top: 10px;
  @include media('>=tablet') {
    right: auto;
  }
  &.arrowTop {
    &::before {
      position: absolute;
      top: -17px;
      left: 211px;
      border-left: 13px solid transparent;
      border-right: 13px solid transparent;
      border-bottom: 12px solid $white;
    }
    &::after {
      display: none;
    }
    @include media('>=tablet') {
      &::before {
        left: 144px;
      }
    }
    @include media('>=desktop') {
      &::before {
        left: 92px;
      }
    }
  }
  .cur-month, .cur-year {
    color: $thunder;
    font-size: 16px;
    line-height: 25px;
  }
  .flatpickr-months {
    padding: 15px 0 0;
    margin-bottom: 12px;
    .flatpickr-prev-month, .flatpickr-next-month {
      top: 9px;
    }
  }
  .flatpickr-current-month {
    font-size: 16px;
    line-height: 25px;
    padding: 0;
    span.cur-month {
      font-weight: normal;
      text-transform: lowercase;
    }
  }
  .flatpickr-weekdays {
    display: none;
  }
  .flatpickr-day {
    color: $hurricane;
    height: 17px;
    line-height: 18px;
    font-size: 16px;
    margin-bottom: 13px;
    background: transparent;
    border: 0;
    &::before {
      content: '';
      position: absolute;
      top: -4px;
      left: 3px;
      width: 25px;
      height: 25px;
      opacity: 0;
      background: $pink_lady;
      border-radius: 50%;
      z-index: -1;
      transition: .25s;
    }
    &.today {
      background: transparent;
      &::before {
        opacity: 1;
        border: 1px solid $hurricane;
        background: transparent;
      }
    }
    &.selected {
      color: $thunder;
      &::before {
        opacity: 1;
        background: $pink_lady;
      }
      &:hover {
        background: transparent;
      }
    }
    &:hover {
      color: $thunder;
      &::before {
        opacity: 1;
        background: $pink_lady;
      }
    }
    &.disabled {
      color: #C4C4C4;
    }
  }
  .flatpickr-days {
    width: 236px;
  }
  .dayContainer {
    width: 236px;
    min-width: 236px;
    max-width: 236px;
    padding: 5px 10px 0;
  }
  .flatpickr-weekdays {
    padding: 0 10px 0;
  }

  .nextMonthDay, .prevMonthDay {
    color: $silver;
    &:hover {
      background: transparent;
    }
  }

}

.search-field {
  position: relative;
  &__input {
    &:focus {
      background: $white;
      border-color: $thunder;
      & + .search-field__button {
        opacity: 0;
        visibility: hidden;
        z-index: 0;
        & + .search-field {
          &__button--close {
            opacity: 1;
            visibility: visible;
            z-index: 1;
          }
        }
      }
    }
  }
  &__button {
    position: absolute;
    top: 50%;
    right: 20px;
    width: 24px;
    height: 24px;
    transform: translateY(-50%);
    cursor: pointer;
    border: 0;
    padding: 0;
    background: transparent;
    transition: .20s;
    z-index: 1;
    &--close {
      width: 20px;
      height: 20px;
      opacity: 0;
      visibility: hidden;
      &::before,
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        width: 20px;
        height: 1px;
        background-color: $thunder;
        transform: translateY(-50%);
      }
      &::before {
        transform: rotate(45deg);
      }
      &::after {
        transform: rotate(-45deg);
      }
    }
  }
}
.search_small-svg {
  width: 24px;
  height: 24px;
  fill: transparent;
}