.privacy-policy {
  background: $pampas;
  @include media('>=tablet') {
    background: transparent;
  }
  .page__wrapper {
    padding-bottom: 200px;
  }
  .page__wrapper-top {
    padding-bottom: 5px;
    @include media('>=tablet') {
      padding-bottom: 40px;
    }
    @include media('>=desktop') {
      padding-bottom: 50px;
    }
    @include media('>=big_desktop') {
      padding-bottom: 80px;
    }
  }
  .page__title {
    margin: 0;
  }
  &__content-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    position: relative;
  }
  &__title-bar {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    @include media('>=big_desktop') {
      display: block;
    }
  }
  &__list {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    list-style: none;
    counter-reset: item;
    &--nested {
      > .privacy-policy__item {
        margin-bottom: 15px;
        &::before {
          content: counter(item) '.';
          font-size: 16px;
          font-weight: normal;
          line-height: 25px;
          color: $thunder;
          margin-right: 6px;
          transition: .25s;
        }
      }
    }
  }
  &__item {
    counter-increment: item;
    color: $thunder;
    margin-bottom: 45px;
    &:last-child {
      margin-bottom: 0;
    }
    @include media('>=tablet') {
      margin-bottom: 40px;
    }
    @include media('>=desktop') {
      margin-bottom: 30px;
    }
  }
  &__title {
    font-weight: 500;
    font-size: 25px;
    font-weight: 500;
    line-height: 25px;
    margin: 0 0 15px;
    &::before {
      content: counter(item) '.';
      font-weight: 500;
      font-size: 21px;
      font-weight: 500;
      line-height: 30px;
      color: $thunder;
      margin-right: 6px;
      transition: .25s;
    }
    @include media('>=tablet') {
      font-size: 25px;
      font-weight: 500;
      line-height: 25px;
    }
  }
  &__text {
    margin-bottom: 15px;
  }
  &__content {
    width: 880px;
  }
}
