.top-section {
  padding: 35px 0;
  background: $pampas;
  @include media(">=tablet") {
    padding: 60px 0 0;
    background: transparent;
  }
  &__form {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include media(">=desktop") {
      flex-direction: column;
    }
  }
  &__title {
    color: $thunder;
    font-weight: 500;
    font-size: 30px;
    line-height: 40px;
    margin: 0 0 7px;
    @include media(">=tablet") {
      font-weight: 600;
      font-size: 55px;
      line-height: 60px;
      margin-bottom: 35px;
    }
    @include media(">=desktop") {
      margin: 0 0 24px -3px;
      display: inline-block;
    }
  }
  &__option {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    @include media(">=tablet") {
      width: auto;
    }
    @include media(">=desktop") {
      align-self: flex-end;
    }
  }
  &__date {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    @include media(">=tablet") {
      margin-left: 50px;
    }
  }
  &__date-button {
    padding-right: 28px;
    cursor: pointer;
    &::before {
      content: "";
      position: absolute;
      top: 33px;
      right: 0;
      width: 0;
      height: 0;
      border: none;
      display: none;
    }
    @include media(">=tablet") {
      &::before {
        border-left: 12px solid transparent;
        border-right: 12px solid transparent;
        border-bottom: 13px solid $pampas;
      }
    }
    @include media(">=desktop") {
      &::before {
        top: 38px;
      }
    }
    &--active {
      .angle-svg {
        transform: rotate(-180deg);
      }
      &::before {
        display: block;
      }
    }
  }
  &__popular {
    display: none;
    margin-bottom: 20px;
    @include media(">=tablet") {
      display: block;
    }
  }
}

.select-region,
.carousel-time {
  &__button-apply {
    width: 100%;
    margin-top: 30px;
    display: block;
    @include media(">=tablet") {
      display: none;
    }
  }
}

.select-region {
  position: relative;
  &__button {
    position: relative;
    padding: 0;
    cursor: default;
    outline: none;
    background: $white;
    border-color: $dusty_gray;
  }
  &__select {
    position: relative;
    padding-right: 27px;
    margin: 0;
    z-index: 2;
    display: none;
  }
  &__span {
    position: absolute;
    top: 2px;
    left: 12px;
    color: $tapa;
    font-size: 16px;
    line-height: 25px;
  }
  &__list {
    display: flex;
    flex-direction: row;
    border-radius: 0 0 2px 2px;
    border: 1px solid $dusty_gray;
    z-index: 10;
    @include media(">=tablet") {
      display: flex;
      flex-direction: column;
      border: 0;
    }
  }
  &__item {
    width: 100%;
    text-align: center;
    line-height: 25px;
    padding: 2px 0;
    color: $thunder;
    transition: 0.2s;
    background: transparent;
    border-right: 1px solid $dusty_gray;
    cursor: pointer;
    &:last-child {
      border-right: 0;
    }
    @include media(">=tablet") {
      padding: 5px 12px;
      border: 0;
      text-align: left;
      &:hover {
        background: $pampas;
      }
      &:last-child {
        border-right: 0;
        border-radius: 0;
      }
    }
    &.selected {
      background: $pink_lady;
      @include media(">=tablet") {
        background: $pampas;
      }
    }
  }
}

.select-region {
  &__mobile {
    position: fixed;
    left: 0;
    bottom: 0;
    left: 50%;
    margin-left: -50vw;
    width: 100vw;
    background: $white;
    padding: 20px;
    transition: 0.2s;
    z-index: 999;
    @include media(">=tablet") {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: auto;
      margin-left: 0;
      padding: 0;
      width: auto;
      background: transparent;
      background: $white;
      border-radius: 2px;
      transform-origin: top left;
      box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.3);
      opacity: 1;
      transform: scale(1, 1);
      transition: 0.1s;
      outline: none;
    }
    &--hide {
      bottom: -100%;
      opacity: 0;
      @include media(">=tablet") {
        bottom: auto;
        transform: scale(1, 0);
      }
    }
    &-span {
      display: block;
      font-weight: 500;
      margin-bottom: 15px;
      @include media(">=tablet") {
        display: none;
      }
    }
  }
  &__selected {
    position: relative;
    color: $thunder;
    font-size: 16px;
    line-height: 25px;
    text-align: left;
    height: 100%;
    background: transparent;
    outline: none;
    cursor: pointer;
    display: block;
    padding: 2px 30px 2px 74px;
    z-index: 3;
  }
}

.angle-svg {
  width: 24px;
  height: 24px;
  fill: none;
  transition: 0.2s;
  &--date-button {
    position: absolute;
    top: 3px;
    right: 0;
  }
  &--top-section {
    position: absolute;
    top: 50%;
    right: 5px;
    height: 24px;
    transform: translateY(-50%);
  }
  &--rotate {
    transform: translateY(-50%) rotate(180deg);
  }
}

.angle-left-svg {
  width: 16px;
  height: 16px;
  &--carousel-next {
    transform: rotate(-180deg);
  }
}

.opened {
  &::before {
    display: block;
  }
}

.carousel-time {
  position: fixed;
  left: 0;
  bottom: -100%;
  left: 50%;
  margin-left: -50vw;
  width: 100vw;
  transition: 0.2s;
  background: $white;
  z-index: 999;
  @include media(">=tablet") {
    position: relative;
    bottom: 0;
    display: none;
    margin-top: 15px;
    background: $pampas;
    z-index: 9;
  }
  @include media(">=desktop") {
    margin-top: 20px;
  }
  &--visible {
    display: block;
    bottom: 0;
  }
  &__form {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include media(">=tablet") {
      align-items: flex-end;
    }
  }
  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    left: 50%;
    bottom: 0;
    height: 100%;
    margin-left: -50vw;
    padding: 20px;
    width: 100vw;
    @include media(">=tablet") {
      align-items: flex-end;
      width: auto;
      margin-left: 0;
      padding: 20px 0 30px;
      left: 0;
      height: 120px;
    }
    @include media(">=desktop") {
      &::before {
        right: 0;
      }
    }
  }
  &__years-wrapper {
    position: relative;
    width: 125px;
    padding: 0 29px;
    @include media(">=tablet") {
      margin-right: 0;
    }
  }
  &__years {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-bottom: 20px;
    @include media(">=tablet") {
      flex-direction: row;
      align-items: center;
      margin-bottom: 0;
    }
  }
  &__list {
    position: relative;
    display: flex;
    transition: transform 0.5s;
    height: 30px;
    overflow: hidden;
  }
  &__item {
    position: absolute;
    top: 0;
    left: 0;
    height: 35px;
    color: $thunder;
    font-weight: 500;
    font-size: 25px;
    width: 100%;
    text-align: center;
    display: none;
    &--active {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }
  }
  &__button {
    position: absolute;
    top: 50%;
    border: none;
    background: transparent;
    width: 16px;
    height: 16px;
    padding: 0;
    transform: translateY(-50%);
    cursor: pointer;
    outline: none;
  }
  &__button-prev {
    left: 0;
  }
  &__button-next {
    right: 0;
  }
  &__checkbox {
    &-label {
      position: relative;
      margin-right: 35px;
      margin-bottom: 15px;
      cursor: pointer;
      @include media(">=tablet") {
        margin-bottom: 0;
      }
    }
    &-input {
      display: none;
      &:checked {
        & + span {
          &::after {
            opacity: 1;
          }
        }
      }
    }
    &-span {
      color: $black;
      width: 100%;
      white-space: nowrap;
      padding-left: 24px;
      font-size: 16px;
      line-height: 25px;
      vertical-align: middle;
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        width: 16px;
        height: 16px;
        border: 1px solid $thunder;
        border-radius: 2px;
        transform: translateY(-50%);
      }
      &::after {
        content: "";
        position: absolute;
        left: 4px;
        top: 50%;
        width: 8px;
        height: 8px;
        border-radius: 2px;
        background-color: $crimson;
        opacity: 0;
        transition: 0.1s;
        transform: translateY(-50%);
      }
    }
  }
}

.select-year {
  display: none;
}

.month-dk {
  position: relative;
  left: 50%;
  width: 100vw;
  height: 31px;
  margin-left: -50vw;
  overflow-x: scroll;
  @include media(">=tablet") {
    position: static;
    width: auto;
    height: auto;
    margin-left: 0;
    padding: 0;
    left: 0;
    bottom: auto;
    overflow-x: hidden;
    margin-top: 7px;
  }
  &__list {
    position: absolute;
    left: 0;
    bottom: 0;
    flex-direction: row;
    justify-content: flex-end;
    padding: 0 20px;
    opacity: 0;
    visibility: hidden;
    transition: 0.2s linear;
    display: none;
    z-index: 0;
    &--active {
      opacity: 1;
      visibility: visible;
      display: flex;
      z-index: 1;
    }
    @include media(">=tablet") {
      padding: 0;
      bottom: 30px;
      left: auto;
      right: 0;
      width: 100%;
    }
  }
  &__item {
    width: 100%;
    border: 1px solid $dusty_gray;
    font-size: 16px;
    line-height: 25px;
    color: $thunder;
    text-align: center;
    padding: 2px 9px;
    border-left: 0;
    background: $white;
    cursor: pointer;
    outline: none;
    transition: 0.2s;
    @include media(">=tablet") {
      padding: 2px 0;
    }
    @include media(">=desktop") {
      padding: 2px 9px;
    }
    &:hover,
    &--active {
      background: $pink_lady;
    }
    &:first-child {
      border-radius: 2px 0 0 2px;
      border-left: 1px solid $dusty_gray;
    }
    &:last-child {
      border-radius: 0 2px 2px 0;
    }
    &._disabled {
      background: $white;
    }
    &._not-available {
      color: $swirl;
      background: transparent;
      cursor: default;
    }
  }
}

.select-month {
  display: none;
}
