.footer-dk {
  position: relative;
  background: $thunder;
  &__wrapper {
    max-width: 785px;
    margin: 0 auto;
    padding: 53px 0 23px 0;
    &--info {
      position: relative;
      padding: 17px 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      @include media(">=desktop") {
        flex-direction: row;
      }
    }
  }
  &__list {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    @include media(">=desktop") {
      flex-direction: row;
    }
  }
  &__item {
    &:last-child {
      @include media(">=desktop") {
        max-width: 200px;
      }
    }
  }
  &__arrow-svg {
    width: 11px;
    height: 12px;
    stroke: $pampas;
  }
  &__info {
    background: #000;
  }
  &__info-text {
    color: $swirl;
    font-size: 12px;
    line-height: 18px;
    margin: 0 0 10px 0;
    max-width: 655px;
    flex-shrink: 1;

    @include media(">=tablet") {
      margin: 0 0 55px 0;
    }

    @include media(">=desktop") {
      margin: 0 20px 0 0;
    }
  }
}

.made-company {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  flex-shrink: 0;
  &__svg {
    width: 25px;
    height: 25px;
    margin-bottom: 20px;

    @include media(">=desktop") {
      margin-bottom: 0;
    }
  }
  &__span {
    color: $swirl;
    font-size: 14px;
    line-height: 20px;
  }
  &__link {
    color: $swirl;
  }
}

.group-footer {
  &__wrapper {
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding-bottom: 15px;
    margin-bottom: 15px;
    @include media(">=desktop") {
      margin-bottom: 25px;
      padding: 0;
      border: none;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__title {
    color: $white;
    margin-bottom: 7px;
    @include media(">=desktop") {
      margin-bottom: 18px;
    }
  }
  &__list {
    display: flex;
    flex-flow: row wrap;
    @include media(">=desktop") {
      display: flex;
      flex-direction: column;
    }
  }
  &__item {
    margin-right: 20px;
    &:last-child {
      margin-bottom: 0;
    }
    &:hover {
      .group-footer__link {
        color: $crimson;
      }
    }
    @include media(">=desktop") {
      display: flex;
      flex-direction: column;
      margin: 0 0 10px 0;
    }
  }
  &__link {
    display: block;
    color: $white;
    font-size: 14px;
    line-height: 20px;
    transition: 0.25s;
  }
  &__span {
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 15px;
    display: block;
  }
  &__form {
    margin-bottom: 10px;
  }
  &__input {
    border: 1px solid #545454;
    background: #000;
    margin-bottom: 10px;
    color: $white;
    font-size: 16px;
    padding: 5px 10px;
    width: 100%;
    outline: none;
  }
  &__button {
    position: relative;
    display: block;
    background: $crimson;
    border: none;
    font-size: 14px;
    padding: 7px 0;
    width: 100%;
    color: $white;
    cursor: pointer;
    transition: 0.25s;
    &:hover {
      background: $hover-button;
    }
    &:active {
      background: $active-button;
    }
  }
  &__text {
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
    line-height: 20px;
    margin: 0;
  }
  &__politics {
    color: $white;
    &:hover {
      text-decoration: underline;
    }
  }
}

.switch {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  &__field {
    margin-right: 10px;
  }
  &__input {
    display: none;

    &:checked + label {
      left: 0;
    }

    &:checked + label:after {
      left: 25px;
      background: $crimson;
      content: "";
    }
  }
  &__label {
    display: block;
    left: 0px;
    top: 0;
    position: relative;
    width: 40px;
    height: 16px;
    z-index: 10;
    line-height: 40px;
    font-weight: bold;
    border-radius: 8px;
    border: 1px solid #535353;
    transition: all 0.2s;
    cursor: pointer;
    &::after {
      content: "";
      width: 10px;
      height: 10px;
      position: absolute;
      left: 3px;
      top: 2px;
      display: block;
      background: $silver;
      transition: all 0.2s;
      border-radius: 50%;
      cursor: pointer;
    }
  }
  &__name {
    font-size: 14px;
    line-height: 20px;
    color: rgba(255, 255, 255, 0.5);
  }
}
