.input-dk,
.textarea-dk {
  color: $thunder;
  font-family: "Fira Sans";
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  width: 100%;
  border: 1px solid $hurricane;
  background: $white;
  padding: 12px 10px;
  border-radius: 2px;
  outline: none;
  transition: all 0.2s ease-out;
  &::placeholder {
    color: $hurricane;
  }
  @include media(">=tablet") {
    background: $pampas;
  }
}

.input-dk {
  padding: 12px 50px 12px 10px;
}

.contacts {
  background: $pampas;
  @include media(">=tablet") {
    background: transparent;
  }
  .page__wrapper {
    padding-bottom: 25px;
    @include media(">=tablet") {
      padding-bottom: 60px;
    }
  }
  .page__wrapper-top {
    padding-bottom: 90px;
    @include media(">=tablet") {
      padding-bottom: 60px;
    }
  }
  &__top {
    display: flex;
    flex-flow: row wrap;
  }
  &__info-wrapper {
    margin-bottom: 70px;
    @include media(">=tablet") {
      margin-bottom: 45px;
    }
    @include media(">=desktop") {
      margin-right: 20px;
      width: 580px;
    }
  }
  &__list {
    width: 100%;
    margin-bottom: 20px;
    @include media(">=tablet") {
      margin-bottom: 10px;
    }
  }
  &__item {
    display: flex;
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__name {
    display: block;
    color: $hurricane;
    margin-right: 20px;
    width: 50%;
    text-align: right;
    flex-shrink: 0;
  }
  &__info {
    display: block;
    color: $thunder;
    &--link {
      &:hover {
        text-decoration: underline;
      }
    }
  }
  &__link-wrapper {
    width: 100%;
    text-align: right;
    @include media(">=tablet") {
      width: 50%;
    }
  }
  &__link {
    color: $thunder;
    text-align: center;
    padding: 2px 0;
    font-size: 14px;
    line-height: 25px;
    display: block;
    border: 1px solid $hurricane;
    border-radius: 2px;
    transition: 0.25s;
    @include media(">=tablet") {
      color: $crimson;
      font-size: 16px;
      display: inline-block;
      padding: 0;
      border: none;
      border-radius: 0;
      border-bottom: 1px solid $crimson;
    }
  }
  &__map {
    width: 100%;
    @include media(">=desktop") {
      width: 580px;
      overflow: hidden;
    }
    iframe {
      border: none !important;
      width: 100% !important;
      height: 280px !important;
    }
  }
}

.feedback {
  position: relative;
  margin-bottom: 50px;
  min-height: 410px;
  @include media(">=tablet") {
    margin-bottom: 75px;
  }
  &__header {
    height: 100%;
  }
  &__title {
    color: $thunder;
    font-weight: 500;
    font-size: 25px;
    line-height: 35px;
    margin: 0 0 10px 0;
  }
  &__text {
    color: $hurricane;
    margin-bottom: 40px;
  }
  &__form {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-end;
    opacity: 1;
    visibility: visible;
    transition: 0.25s;
    &--hide {
      opacity: 0;
      visibility: hidden;
    }
  }
  &__left {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-direction: column;
    justify-content: space-between;
    @include media(">=desktop") {
      margin: 0 120px 0 0;
      width: 280px;
    }
    .feedback__field {
      margin-bottom: 40px;
      @include media(">=desktop") {
        margin-bottom: 0;
      }
    }
  }
  &__right {
    width: 100%;
    @include media(">=desktop") {
      width: 480px;
    }
    @include media(">=big_desktop") {
      width: 780px;
    }
  }
  &__field {
    position: relative;
  }
  &__label {
    position: absolute;
    top: 14px;
    left: 10px;
    color: $hurricane;
    font-size: 16px;
    line-height: 25px;
    transition: all 0.18s ease-in-out;
    cursor: text;
    transform: scale(1);
    transform-origin: 0 100%;
  }
  &__textarea {
    height: 100%;
    min-height: 230px;
    display: block;
    resize: none;
  }
  &__input {
    min-height: 50px;
    padding: 20px 10px 8px;
  }
  &__success {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    visibility: hidden;
    transition: 0.25s;
    &-title {
      color: $hurricane;
      font-style: normal;
      font-weight: 500;
      line-height: 25px;
      font-size: 16px;
      margin: 0 0 20px 0;
    }
    &--visible {
      opacity: 1;
      visibility: visible;
      z-index: 1;
    }
  }
  &__button-apply {
    width: auto;
    margin: 0 auto;
    padding: 2px 15px;
  }
  .focus {
    background: $white;
    border-color: $thunder;
    & + label {
      color: $thunder;
      top: -2px;
      transform: scale(0.8);
    }
  }
  .button--send {
    width: 100%;
    margin-top: 30px;
    padding: 14px 99px;
    @include media(">=desktop") {
      width: 280px;
      margin-right: auto;
    }
  }
}

.section-employees {
  margin-bottom: 40px;
  &:last-child {
    margin-bottom: 0;
  }
  @include media(">=tablet") {
    margin-bottom: 70px;
  }
  &__title {
    color: $thunder;
    font-weight: 500;
    font-size: 25px;
    line-height: 35px;
    margin: 0 0 60px 0;
  }
  &__list {
    display: flex;
    flex-flow: row wrap;
  }
  &__item {
    margin-bottom: 40px;
    @include media(">=tablet") {
      margin-right: 20px;
      &:nth-of-type(2n) {
        margin-right: 0;
      }
    }
    @include media(">=desktop") {
      &:nth-of-type(2n) {
        margin-right: 20px;
      }
      &:nth-of-type(3n) {
        margin-right: 0;
      }
    }
  }
}
