.note {
  position: relative;
  padding-top: 55px;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;;
    width: 100vw;
    height: 100%;
    margin-left: -50vw;
    background: $pampas;
    display: block;
    z-index: -1;
  }
  @include media('>=tablet') {
    background: transparent;
    padding-top: 30px;
    &::before {
      display: none;
    }
  }
  @include media('>=big_desktop') {
    padding-top: 0;
  }
  &__wrapper {
    padding: 7px 25px 35px;
    border: 5px solid $black;
    background: $white;
    @include media('>=tablet') {
      padding: 7px 15px 65px;
    }
    @include media('>=big_desktop') {
      padding: 25px 45px 85px;
    }
  }
  &__title-wrapper {
    margin: 35px 0 30px;
    display: flex;
    justify-content: space-between;
    @include media('>=tablet') {
      margin: 65px 0 0;
    }
    @include media('>=big_desktop') {
      position: relative;
      margin: 40px 0 50px;
    }
  }
  &__image {
    max-width: 180px;
    max-height: 180px;
    overflow: hidden;
    position: absolute;
    top: 55px;
    right: 0;
    @include media('>=tablet') {
      top: 0px;
      left: 0;
      right: auto;
      max-width: 100px;
      max-height: 100px;
    }
    @include media('>=big_desktop') {
      top: -55px;
      left: auto;
      right: 10px;
      max-width: 180px;
      max-height: 180px;
    }
  }
  &__title {
    position: absolute;
    top: 31px;
    right: 0;
    font-weight: normal;
    font-size: 14px;
    letter-spacing: 1.2px;
    line-height: 20px;
    color: $black;
    margin: 0;
    text-transform: uppercase;
    &::after {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      width: 115%;
      height: 5px;
      display: none;
      background: $thunder;
    }
    @include media('>=tablet') {
      top: 6px;
    }
    @include media('>=big_desktop') {
      position: relative;
      font-weight: 600;
      font-size: 40px;
      line-height: 50px;
      text-transform: none;
      margin-bottom: 10px;
      padding-bottom: 10px;
      &::after {
        display: block;
      }
    }
  }
  &__name {
    color: $thunder;
    font-weight: 500;
    font-size: 25px;
    line-height: 35px;
    margin: 0;
    max-width: 240px;
  }
  &__text {
    color: $black;
    font-family: 'PT Serif';
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 20px;
  }
  &__link {
    color: $crimson;
    line-height: 30px;
    border-bottom: 1px solid $crimson;
    transition: .20s;
    cursor: pointer;
    &:hover {
      color: $crimson;
      border-color: $crimson;
    }
    @include media('>=tablet') {
      color: $thunder;
      border-color: $thunder;
    }
  }
  &__dots {
    position: relative;
    left: -10px;
    @include media('>=tablet') {
      left: -20px;
    }
  }
}