.banner {
  position: relative;
  width: 100%;
  height: 240px;
  display: block;
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  cursor: pointer;
  &__img {
    background-color: $banner;
  }
  &--full-container {
    left: 50%;
    width: 100vw;
    max-height: 240px;
    height: auto;
    margin-left: -50vw;
    @include media(">=big_desktop") {
      left: 0;
      margin-left: 0;
      width: 100%;
    }
  }
  &--full-width {
    left: 50%;
    width: 100vw;
    max-height: 240px;
    height: auto;
    margin-left: -50vw;
  }
  &--square {
    width: calc(100% + 40px);
    margin-left: -20px;
    height: 240px;
    @include media(">=tablet") {
      width: 280px;
      height: 280px;
      margin-left: 0;
    }
    &-big {
      height: 420px;
      @include media(">=tablet") {
        width: 580px;
        height: 580px;
        margin-left: 0;
      }
    }
  }
  &--square-detail {
    width: 200px;
    height: 200px;
  }
  &--rectangle {
    &-detail {
      width: 200px;
      height: 300px;
    }
    &-vertical {
      width: 100%;
      height: 580px;
      @include media(">=tablet") {
        width: 280px;
      }
    }
  }
}

._square-mobile {
  &.article-page__banner {
    position: static;
    display: block;
    margin-top: 25px;
    margin-left: auto;
    margin-right: auto;
    @include media(">=desktop") {
      display: none;
    }
  }
}
